const { regClass, property } = Laya;
import { Egg } from "../GameLogic/Egg";
import Sprite = Laya.Sprite
@regClass('9d_4XDZ6SpOvM9UXuhW-Bg')
export class CartoonFace extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;

    @property([String])
    public eyes: string[];

    @property([String])
    public mouth: string[];

    @property(Sprite)
    public eyesRoot: Sprite;


    @property(Sprite)
    public mouthRoot: Sprite;

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    onAwake(): void {
        //关闭默认的
        const defaultEye = this.eyesRoot.getChildAt(0) as Sprite;
        defaultEye.visible = false;

        const defaultMouth = this.mouthRoot.getChildAt(0) as Sprite;
        defaultMouth.visible = false;
    }

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    public build ( index: number) {
        //随机
        const eyeIndex = Math.floor(index % this.eyes.length);
        const eye = this.eyesRoot.getChildByName(this.eyes[eyeIndex]) as Sprite;
        if (eye) {
            eye.visible = true;
        } else {
            console.error("eye is null");
        }

        const mouthIndex = Math.floor(index % this.mouth.length);
        const mouth = this.mouthRoot.getChildByName(this.mouth[mouthIndex]) as Sprite;
        mouth.visible = true;

        this.startBlinking();
        this.startTalking();
    }

    public startBlinking(): void {
        this.blink(); // 立即开始眨眼动画
    }
    
    private blink(): void {
    
        // 闭眼，通过缩放实现
        Laya.Tween.to(this.eyesRoot, { scaleY: 0 }, 150, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
            // 睁眼，恢复缩放
            Laya.Tween.to(this.eyesRoot, { scaleY: 1 }, 150, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
                // 随机间隔再眨眼
                const randomInterval = 3000 + Math.random() * 3000;
                Laya.timer.once(randomInterval, this, this.blink);
            }));
        }));
    }

    public startTalking(): void {
        this.talk(); // 立即开始嘴巴动画
    }
    
    private talk(): void {
    
        // 闭嘴，缩小 scaleY
        Laya.Tween.to(this.mouthRoot, { y: this.mouthRoot.y + 10 }, 200, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
            // 向上移动，模拟张嘴
            Laya.Tween.to(this.mouthRoot, { y: this.mouthRoot.y - 10 }, 200, Laya.Ease.linearNone, Laya.Handler.create(this, () => {
                // 随机间隔后再执行动作
                const randomInterval = 5000 + Math.random() * 5000;
                Laya.timer.once(randomInterval, this, this.talk);
            }));
        }));
    }

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    onUpdate(): void {

    }

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}