import { ConfigHelper, ConfigManager } from "../../Config/ConfigManager";
import { ItemConfig } from "../../Config/ExcelItem";
import { MachineConfig } from "../../Config/ExcelMachine";
import { RunTimeDataManager } from "../../RunTimeData/RunTimeDataManager";
import { UIBase } from "../UIBase";
import { UIClassName, UIDefine } from "../UIDefine";
import { UIManager } from "../UIManager";

export class UICollection extends UIBase {
    
    private _listCollection : fgui.GList;
    private _listPageSelect : fgui.GList;
    private _textMessage: fgui.GTextField;
    private _maxOnePageCollectionNumber: number = 9;
    private _collectionConfigList: string[] = [];
    private _curDisplayList: string[] = [];

    public initUI(): void {
        super.initUI();

        this._listCollection = this.view.getChild("LIST_Collection").asList;
        this._listPageSelect = this.view.getChild("LIST_PageSelect").asList;
        this._textMessage = this.view.getChild("TEXT_Message").asTextField;

        this._listPageSelect.itemRenderer = Laya.Handler.create(this, this.renderListPageSelect, null, false);
        this._listPageSelect.on(fgui.Events.CLICK_ITEM, this, this.onClickListPageSelectItem);

        this._listCollection.itemRenderer = Laya.Handler.create(this, this.renderListCollection, null, false);
        this._listCollection.on(fgui.Events.CLICK_ITEM, this, this.onClickItem);   
    }

    public didOpen(): void {
        super.didOpen();
        const curMachineID = RunTimeDataManager.instance.runTimeData.machine_data.id;
        const machineConfig = MachineConfig.getConfig(curMachineID);
        const collectionList = ConfigHelper.parseStringArray(machineConfig.CollectionList)
        this._collectionConfigList = collectionList;
        
        let collectionPageNumber = collectionList.length / this._maxOnePageCollectionNumber;
        if (collectionList.length % this._maxOnePageCollectionNumber != 0) {
            collectionPageNumber += 1;
        }

        this._listPageSelect.numItems = collectionPageNumber;
        this._listPageSelect.selectedIndex = 0;
        this.refreshCollectionByBageIndex(this._listPageSelect.selectedIndex);

        this._textMessage.text = `You have: [color=#ffff99]${RunTimeDataManager.instance.runTimeData.machine_collection.getItemCount()}/${this._collectionConfigList.length}[/color]`
    }

    private renderListCollection(index: number, obj: fgui.GObject): void {
        const itemConfig = ItemConfig.getConfig(this._curDisplayList[index]);
        const loader = obj.asCom.getChild("LOADER_ItemIcon").asLoader;
        loader.url = itemConfig.Texture;
        
        const textCount = obj.asCom.getChild("TEXT_Count").asLabel;
        const count = RunTimeDataManager.instance.runTimeData.machine_collection.getItemCountById(itemConfig.ID)
        if (count > 0) {
            loader.color = "#FFFFFF";
            textCount.text = "x" + count;
        } else {
            textCount.text = "";
            loader.color = "#000000";
        }

        obj.data = itemConfig.ID;
    }

    private refreshCollectionByBageIndex(pageIndex: number): void {

        this._listCollection.numItems = 0;
        this._curDisplayList = [];

        let startIndex = pageIndex * this._maxOnePageCollectionNumber;
        let endIndex = (pageIndex + 1) * this._maxOnePageCollectionNumber;

        for (let i = startIndex; i < endIndex; i++) {
            if (i >= this._collectionConfigList.length) {
                break;
            }

            const itemConfig = ItemConfig.getConfig(this._collectionConfigList[i]);
            if (itemConfig) {
                this._curDisplayList.push(this._collectionConfigList[i]);
            }
        }

        this._listCollection.numItems = this._curDisplayList.length;
    }

    private onClickListPageSelectItem(): void {
        this.refreshCollectionByBageIndex(this._listPageSelect.selectedIndex);
    }

    private onClickItem(item: fgui.GObject): void {
        //console.log("onClickItem " + item.data);
        const itemID = item.data;
        const count = RunTimeDataManager.instance.runTimeData.machine_collection.getItemCountById(itemID)
        if (count > 0) {
            UIManager.instance.pushUI(UIClassName.UI_ShowItem_HUD, {
                itemID: itemID,
                count: count
            })
        } else {
            UIManager.instance.ShowMessageBox("You don't have this item", null);
        }
    }

    private renderListPageSelect(index: number, obj: fgui.GObject): void {

        const btn = obj.asButton;

        const n1 = btn.getChild("TEXT_N1").asLabel;
        const n2 = btn.getChild("TEXT_N2").asLabel;

        n1.text = "" + (index + 1);
        n2.text = "" + (index + 1);
    }
}