import { ParticleSystem, Particle} from "./ParticleSystem";

const { regClass, property } = Laya;

class RandomPositionParticle extends Particle {

    startSize: number;
    endSize: number;
    
    startAlpha: number;
    endAlpha: number;

    constructor(x: number, y: number, texture: Laya.Texture) {
        super(x, y, texture, true); // 将其设为循环粒子
    }

    pingPong(t: number, min: number, max: number): number {
        // Normalize t to range [0, 1]
        const range = max - min;
        const normalizedTime = t % (2 * range);
        
        // 如果在 [0, range] 区间内，直接返回 min + normalizedTime
        // 如果在 [range, 2 * range] 区间内，返回 max - (normalizedTime - range)
        return normalizedTime < range ? 
            min + normalizedTime : 
            max - (normalizedTime - range);
    }

    // 重置位置为随机值
    reset(x: number, y: number, texture: Laya.Texture): void {
        // 随机生成位置
        const randomX = (Math.random() * 150); // 基于传入位置随机
        const randomY = (Math.random() * 150); // 基于传入位置随机

        super.reset(randomX, randomY, texture);
        this.setLife(100, 200)
        this.setSize(0.5, 1)
        // 重新设置粒子的其他属性
        this.life = Math.random() * (this.maxLife - this.minLife) + this.minLife; // 生命周期调整
        this.fixedLife = this.life;

        //this.size = Math.random() * (this.maxSize - this.minSize) + this.minSize; // 随机大小
        this.startSize = 1;
        this.endSize = 3;

        this.startAlpha = 0;
        this.endAlpha = 1;


        this.size = this.startSize;
        this.alpha = 0; // 初始透明度
    }

    // 重写 update 方法实现随机漂浮
    update(): void {

        // 计算进度，假设生命周期最大值为 maxLife
        const progress = 1 - (this.life / this.fixedLife);
        
        // 使用 pingPong 函数更新大小和透明度
        this.size = this.pingPong(progress, this.startSize, this.endSize);
        this.alpha = this.pingPong(progress * 2, this.startAlpha, this.endAlpha);
        
        // 调整生命值
        this.life -= 1; // 非循环粒子减少生命

        // 如果是循环粒子，检查生命周期以重置
        if (this.isLoop && this.life <= 0) {
            this.reset(this.x, this.y, this.texture); // 重置为新的随机状态
        }
    }
}


@regClass('JOuJPy-aRF6Gf2uzJwRh_g')
export class FXStart extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;
    public particleSystem: ParticleSystem<RandomPositionParticle>;

    onStart() {
        const texture = this.owner.texture;
        this.owner.texture = null;
        this.particleSystem = new ParticleSystem(texture, 20);
        this.particleSystem.emit(0, 0, RandomPositionParticle, 5);

        // 开始更新和绘制粒子系统
        Laya.timer.frameLoop(1, this, this.onParticleUpdate);
    }

    onTextureLoaded(): void {
        // 获取已加载的纹理
    }

    onParticleUpdate(): void {
        this.particleSystem.update();  // 更新粒子
        this.particleSystem.draw(this.owner);  // 在 LayaAir 的 sprite 上绘制粒子
    }

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    //onAwake(): void {}

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}