import { ParticleSystem, Particle } from "./ParticleSystem";

const { regClass, property } = Laya;

class ExplosionParticle extends Particle {
    startSize: number;
    endSize: number;

    startAlpha: number;
    endAlpha: number;
    
    color: string; // 添加颜色属性
    gravity: number = 0.2; // 重力效果

    constructor(x: number, y: number, texture: Laya.Texture) {
        super(x, y, texture, false); // 非循环粒子
    }

    reset(x: number, y: number, texture: Laya.Texture): void {
        const randomX = (Math.random() * 150) - 75; // 随机生成位置
        const randomY = (Math.random() * 150) - 75;

        super.reset(randomX, randomY, texture);
        this.setLife(100, 200);
        this.setSize(0.5, 1);
        
        this.life = Math.random() * (this.maxLife - this.minLife) + this.minLife;
        this.fixedLife = this.life;

        const angle = Math.random() * Math.PI * 2;
        const speed = Math.random() * 10 + 2;
        this.vx = Math.cos(angle) * speed;
        this.vy = Math.sin(angle) * speed;

        this.startSize = 1;
        this.endSize = 3;

        this.startAlpha = 1; // 初始透明度
        this.endAlpha = 0; // 结束时完全透明

        // 随机生成颜色
        const r = Math.random();
        const g = Math.random();
        const b = Math.random();
        this.color = `rgba(${Math.floor(r * 255)}, ${Math.floor(g * 255)}, ${Math.floor(b * 255)}, 1)`;

        this.size = this.startSize;
        this.alpha = this.startAlpha;
    }

    update(): void {
        this.x += this.vx;
        this.y += this.vy;

        this.vy += this.gravity; // 应用重力效果

        const progress = 1 - (this.life / this.fixedLife);
        this.size = this.startSize + (this.endSize - this.startSize) * progress;
        this.alpha = this.startAlpha - (this.startAlpha - this.endAlpha) * progress;

        this.life -= 1;

        // 处理粒子生命结束
        if (this.life <= 0) {
            this.alpha = 0; // 完全透明
        }
    }

    draw(sprite: Laya.Sprite): void {
        sprite.graphics.drawTexture(
            this.texture,
            this.x - (this.width * this.size) / 2,
            this.y - (this.height * this.size) / 2,
            this.width * this.size,
            this.height * this.size,
            null,
            this.alpha,
            this.color
        );
    }
}



@regClass('TFSmKJ9qS7e3rXvq2nuTug')
export class FXExplosion extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;
    public particleSystem: ParticleSystem<ExplosionParticle>;

    onStart() {
        const texture = this.owner.texture;
        this.owner.texture = null;
        this.particleSystem = new ParticleSystem(texture, 20);
        
        // 开始更新和绘制粒子系统
        Laya.timer.frameLoop(1, this, this.onParticleUpdate);
    }

    public emit(){
        this.particleSystem.emit(0, 0, ExplosionParticle, 20);
    }

    onTextureLoaded(): void {
        // 获取已加载的纹理
    }

    onParticleUpdate(): void {
        this.particleSystem.update();  // 更新粒子
        this.particleSystem.draw(this.owner);  // 在 LayaAir 的 sprite 上绘制粒子
    }

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    //onAwake(): void {}

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}