import { RunTimeDataManager } from "../RunTimeData/RunTimeDataManager";

const { regClass, property } = Laya;

@regClass('DGCskiILRi65QciOt7TFbg')
export class HttpManager extends Laya.Script {  
    private static _instance: HttpManager;  
  
    public static get instance(): HttpManager {  
        if (!HttpManager._instance) {  
            return null;
        }  
        return HttpManager._instance;  
    }  
  
    private _session: string;

    constructor() {  
        super();  
    }
  
    //private baseUrl: string = "http://127.0.0.1:9100";  // 替换为你的服务器 API 基础 URL  
    private baseUrl: string = "https://seed-test.9x9168.com:9100";  // 替换为你的服务器 API 基础 URL 
  
    public get(endpoint: string, params: Object = {}, callback: Function, errorCallback: Function = null, progressCallback: Function = null): void {  
        const url = this.buildUrl(endpoint, params);  
        const httpRequest = new Laya.HttpRequest();  
        
        httpRequest.once(Laya.Event.COMPLETE, self, (evt:any)=>{
            //console.log(evt);
            if (evt.err_no != 0)  {
                if(errorCallback) {
                    errorCallback(evt.data);
                }
                return;
            }

            RunTimeDataManager.instance.saveData(evt.data);
            if(callback) {
                callback(evt.data);
            }
        });

        httpRequest.once(Laya.Event.ERROR, self, (error:any)=>{
            //console.log(error);
            if(errorCallback) {
                errorCallback(error.data);
            }
        });

        httpRequest.on(Laya.Event.PROGRESS, self, (evt:any)=>{
            //console.log(evt);
            if(progressCallback) {
                progressCallback(evt.data);
            }
        });

        const headers = this._session ? ["Authorization", "" + this._session] : [];
        httpRequest.send(url, null, "get", "json", headers);
    }  
  
    public post(endpoint: string, data: Object, callback: Function, errorCallback: Function = null, progressCallback: Function = null): void {  
        const url = this.baseUrl + endpoint;  
        const httpRequest = new Laya.HttpRequest();  
        
        httpRequest.http.timeout = 10000;
        //send(url: string, data?: any, method?: "get" | "post" | "head", responseType?: "text" | "json" | "xml" | "arraybuffer", headers?: string[]): void;
        httpRequest.once(Laya.Event.COMPLETE, self, (evt:any)=>{
 
            if(evt.data != null && evt.data.game_session != null){
                this._session = evt.data.game_session;
            }
            
            if (evt.err_no != 0) {
                if(errorCallback) {
                    errorCallback(evt.err_msg);
                }
                return;
            }

            RunTimeDataManager.instance.saveData(evt.data);
            
            if(callback) {
                callback(evt.data);
            }
        });

        httpRequest.once(Laya.Event.ERROR, self, (error:any)=>{
            //console.log(error);
            if(errorCallback) {
                errorCallback(error.data);
            }
            
        });

        httpRequest.once(Laya.Event.PROGRESS, self, (data:any)=>{
            //console.log(evt);
            if(progressCallback) {
                progressCallback(data.data);
            }
        });

        const headers = this._session ? ["Authorization", "" + this._session] : [];
        httpRequest.send(url, data, "post", "json", headers);
    }  

    private buildUrl(endpoint: string, params: Object): string {  
        let url = this.baseUrl + endpoint;  
        const queryString = Object.keys(params)  
            .map(key => `${encodeURIComponent(key)}=${encodeURIComponent(params[key])}`)  
            .join('&');  
          
        if (queryString) {  
            url += '?' + queryString;  
        }
          
        return url;  
    }

    onAwake(): void {
        if (HttpManager._instance) {
            throw new Error("Singleton class. Use getInstance() instead.");
        }
        HttpManager._instance = this;
    }
}

// 定义一个辅助函数，将回调函数转换为返回 Promise 的形式  
export function postWithPromise(httpManager: HttpManager, url: string, data: any): Promise<any> {  
    return new Promise((resolve, reject) => {  
        httpManager.post(url, data, (response: any) => {  
            resolve(response);  
        }, (error: any) => {  
            reject(error);  
        });  
    });  
}  

export function getWithPromise(httpManager: HttpManager, url: string, params: any): Promise<any> {  
    return new Promise((resolve, reject) => {  
        httpManager.get(url, params, (response: any) => {  
            resolve(response);  
        }, (error: any) => {  
            reject(error);  
        });  
    });  
}  