import { SoundFXManager } from "../Component/SoundManager";
import Sprite = Laya.Sprite;
import Tween = Laya.Tween;
import Handler = Laya.Handler;
import Ease = Laya.Ease;
import Point = Laya.Point;

export class CoinFlyEffect {
    private coins: Sprite[] = [];
    private target: Sprite; // The target icon (e.g., a coin bag or player avatar)
    private coinCount: number;
    private targetPos: { x: number, y: number };
    private range: number;
    private width = 50;  // Set coin size
    private height = 50; // Set coin size
    private texture: Laya.Texture;
    private sfx: string;

    constructor(coinCount: number, target: Sprite, range: number, sftx: string) {
        this.coinCount = coinCount;
        this.target = target;
        this.range = range;
        this.width = target.width;
        this.height = target.height;
        this.texture = target.texture;
        this.sfx = sftx;

        let targetWorldPos: Point = Point.create();
        targetWorldPos.x = target.width * target.anchorX;
        targetWorldPos.y = target.height * target.anchorY;
        targetWorldPos = target.localToGlobal(targetWorldPos);

        this.targetPos = { x: targetWorldPos.x, y: targetWorldPos.y }; // The target's position
    }

    // Initialize the coins and start the flying effect
    public startEffect(x: number, y: number) {
        // Generate the coins
        for (let i = 0; i < this.coinCount; i++) {
            const coin = this.createCoin();
            this.coins.push(coin);
            Laya.stage.addChild(coin);

            // Randomize starting position of coins
            const angle = Math.random() * 2 * Math.PI; // 随机生成一个 0 到 2*PI 的角度
            const radius = Math.random() * this.range; // 随机生成一个半径，范围是 0 到 this.range

            const startX = x + Math.cos(angle) * radius; // 根据角度和半径计算 X 坐标
            const startY = y + Math.sin(angle) * radius; // 根据角度和半径计算 Y 坐标

            coin.pos(startX, startY);

            // Animate each coin toward the target
            this.animateCoin(coin, this.targetPos.x, this.targetPos.y);
        }
    }

    // Create a coin sprite
    private createCoin(): Sprite {
        const coin = new Sprite();
        coin.texture = this.texture;
        //coin.loadImage(this.image); // Load your coin image
        coin.width = this.width;  // Set coin size
        coin.height = this.height; // Set coin size
        return coin;
    }

    // Animate a coin from start to target position
    private animateCoin(coin: Sprite, targetX: number, targetY: number) {
        const duration = 1000 + Math.random() * 500; // Randomize animation time
        Tween.to(coin, { scaleX: 1.2, scaleY: 1.2 }, duration * 0.3, Ease.cubicInOut, Handler.create(this, () => {
            Tween.to(coin, { x: targetX, y: targetY, scaleX: 1, scaleY: 1 }, duration, Ease.cubicIn, Handler.create(this, () => {
                SoundFXManager.playSoundFX(this.sfx);
                coin.removeSelf(); // Remove the coin once the animation is done
            }));
        }));
    }
}
