const { regClass, property } = Laya;
import { IncubationEntry } from "../RunTimeData/RunTimeData";
import Label = Laya.Label;
import Button = Laya.Button;
import Sprite = Laya.Sprite;
import Event = Laya.Event;
import Point = Laya.Point;
import { CountdownTimer } from "../Component/CountdownTimer";
import { EggConfig } from "../Config/ExcelEgg";
import { Egg } from "./Egg";
import { UIManager } from "../UI/UIManager";
import { UIClassName } from "../UI/UIDefine";
import { UIShowEggIncuabtion } from "../UI/HUD/UIShowEggIncuabtion";
import { SoundFXManager } from "../Component/SoundManager";

export enum IncubationSlotState {
    None,
    LOCK,
    EMPTY,
    INCUBATION,
    READY,
}

@regClass('-k2-SsuaRu2uvaXAK8ZJzQ')
export class IncubationSlot extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;
    public index: number = 0;
    private _curState : IncubationSlotState = IncubationSlotState.None;

    @property(Label)
    public textCooldownTime: Label;
    private _countdownTimer : CountdownTimer;
    
    @property(Button)
    public bReady: Button;

    @property(Button)
    public bUnlock: Button;

    @property(Button)
    public bInfo: Button;

    @property(Sprite)
    public icon: Sprite;
    private _iconScale = 0;
    private _iconOriginY = 0;

    public onHarvestEggCallback: (soltIndex: number, worldPos: Point) => void; 
    private _incubationEntry: IncubationEntry;
    private _curEgg: Egg;
    
    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    onAwake(): void {


        this.setState(IncubationSlotState.LOCK);
        this.bReady.on(Event.CLICK, this, this.onClickReady);
        this.bInfo.on(Event.CLICK, this, this.onClickInfo);
        this.bUnlock.on(Event.CLICK, this, this.onClickUnlock);
    }

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    onStart(): void {
        
    }

    onClickInfo(e: Event = null) {
        SoundFXManager.playSoundFX("SFX_ButtonClick01");
        //console.log("Click Ready");
        if (this._curState != IncubationSlotState.INCUBATION) return;

        UIManager.instance.pushUI(UIClassName.UI_ShowEggIncuabtion_HUD, {
            incubationIndex: this.index
        });
    }

    onClickReady(e: Event = null) {
        SoundFXManager.playSoundFX("SFX_ButtonClick01");
        //console.log("Click Ready");

        let worldPos: Point = Point.create();
        worldPos.x = this.icon.width * this.icon.anchorX;
        worldPos.y = this.icon.height * this.icon.anchorY;
        worldPos = this.icon.localToGlobal(worldPos);

        if(this.onHarvestEggCallback != null){
            this.onHarvestEggCallback(this.index, worldPos);
        }
    }

    onClickUnlock(e: Event = null) {
        SoundFXManager.playSoundFX("SFX_ButtonClick01");
        //console.log("Click Ready");
        if (this._curState != IncubationSlotState.LOCK) return;

        UIManager.instance.pushUI(UIClassName.UI_UnlockEggIncuabtion_HUD, null);
    }

    setState(state: IncubationSlotState) {

        if(this._curState == state) return;

        switch (state) {
            case IncubationSlotState.LOCK:
                this.bReady.visible = false;
                this.bUnlock.visible = true;
                this.icon.visible = false;
                this.textCooldownTime.visible = false;
                this.clear();
                break;
            case IncubationSlotState.EMPTY:
                this.bReady.visible = false;
                this.bUnlock.visible = false;
                this.icon.visible = false;
                this.textCooldownTime.visible = false;
                this.clear();
                break;
            case IncubationSlotState.INCUBATION:
                this.bReady.visible = false;
                this.bUnlock.visible = false;
                this.icon.visible = true;
                this.textCooldownTime.visible = true;
                this.initTimeShower();
                this.initIcon();
                break;
            case IncubationSlotState.READY:
                this.bReady.visible = true;
                this.bUnlock.visible = false;
                this.icon.visible = true;
                this.textCooldownTime.visible = false;
                this.initIcon();
                this.startIconBounceAnimationReady();
                break;
        }

        this._curState = state;
    }

    clear() {
        if(this._countdownTimer != null) {
            this._countdownTimer.stop();
            this._countdownTimer = null;
        }

        this.textCooldownTime.text = "";

        // 停止跳动动画
        this.stopIconBounceAnimation();

        if(this._curEgg != null) {
            this._curEgg.owner.destroy();
            this._curEgg = null;
        }
    }

    initIcon() {
        if(this._curEgg != null) return;

        const eggConfig = EggConfig.getConfig(this._incubationEntry.config_id);
        Laya.loader.load(eggConfig.Prefab).then(res => {
            let sprite: Laya.Sprite = res.create();
            sprite.anchorX = 0.5;
            sprite.anchorY = 0.5;

            this.icon.addChild(sprite);

            // 获取 icon 和 sprite 的宽高
            let aWidth = this.icon.width;
            let aHeight = this.icon.height;

            let bWidth = sprite.width * 1.2;
            let bHeight = sprite.height * 1.2;

            // 计算宽高比例
            let scaleX = aWidth / bWidth;
            let scaleY = aHeight / bHeight;

            // 取最小的比例来保证等比缩放
            let scale = Math.min(scaleX, scaleY);

            // 应用等比缩放
            sprite.scale(scale, scale, true);

            // 将 sprite 放置在 icon 的中心位置
            //sprite.pos(aWidth / 2, aHeight / 2);
            
            // 将 sprite 放置在 icon 的中心位置
            sprite.pos(aWidth / 2, aHeight / 2 + 80);
            sprite.anchorY = 1;

            const egg = sprite.getComponent(Egg);
            egg.removeRigidBody();
            egg.eggID = this._incubationEntry.egg_id;
            egg.init();

            this._curEgg = egg;
            this._iconScale = sprite.scaleY;
            this._iconOriginY = sprite.y;

            switch (this._curState) {

                case IncubationSlotState.INCUBATION:
                    this.startIconBounceAnimationIncubation();
                    break;
                case IncubationSlotState.READY:
                    this.startIconBounceAnimationReady();
                    break;
            }
        }); 
    }

    // 停止跳动动画
    private stopIconBounceAnimation() {
        Laya.Tween.clearTween(this.icon); // 清除 this.icon 上的所有动画
    }

    // 给icon增加一个跳动的动画效果
    private startIconBounceAnimationIncubation() {
        // 首先停止之前可能存在的动画
        this.stopIconBounceAnimation(); 

        if (this._curEgg == null) return;

        // 动画函数：让icon缩放变大并稍微上移，带有弹力效果
        const bounce1 = () => {
            Laya.Tween.to(this._curEgg.owner, { scaleX: this._iconScale * 1.1, scaleY: this._iconScale * 0.8 }, 1000, Laya.Ease.linearInOut, Laya.Handler.create(this, bounce2));
        };

        // 动画函数：让icon缩放变小并回到原位置，带有弹力效果
        const bounce2 = () => {
            Laya.Tween.to(this._curEgg.owner, { scaleX: this._iconScale, scaleY: this._iconScale }, 1000, Laya.Ease.linearInOut, Laya.Handler.create(this, bounce1));
        };

        // 开始第一次动画
        bounce1();
    }

    private startIconBounceAnimationReady() {
        // 首先停止之前可能存在的动画
        this.stopIconBounceAnimation(); 

        if (this._curEgg == null) return;

        // 动画函数：让icon缩放变大并稍微上移，带有弹力效果
        const bounce1 = () => {
            Laya.Tween.to(this._curEgg.owner, { scaleX: this._iconScale * 1.1,  scaleY: this._iconScale * 0.8 }, 1000, Laya.Ease.backOut, Laya.Handler.create(this, bounce2));
        };

        // 动画函数：让icon缩放变小并回到原位置，带有弹力效果
        const bounce2 = () => {
            Laya.Tween.to(this._curEgg.owner, { scaleX: this._iconScale * 1, scaleY: this._iconScale * 1, y: this._iconOriginY - 100}, 200, Laya.Ease.circInOut, Laya.Handler.create(this, bounce3));
        };

        // 动画函数：让icon缩放变小并回到原位置，带有弹力效果
        const bounce3 = () => {
            Laya.Tween.to(this._curEgg.owner, { scaleX: this._iconScale * 1, scaleY: this._iconScale * 1, y: this._iconOriginY }, 1000, Laya.Ease.bounceOut, Laya.Handler.create(this, bounce1));
        };

        // 开始第一次动画
        bounce1();
    }

    initTimeShower() {

        if(this._countdownTimer != null) {
            this._countdownTimer.restart(this._incubationEntry.maturation_at.T)
            return;
        }

        this._countdownTimer = new CountdownTimer(this._incubationEntry.maturation_at.T, 
            (remainingTime, difference) => {
                this.textCooldownTime.text = `${remainingTime}`

            },
            (remainingTime, difference) => {
                this.textCooldownTime.text = `${remainingTime}`
            },
            () => {
                this.setState(IncubationSlotState.READY);
            }
        );
        this._countdownTimer.start();
    }

    updateSlot(incubationEntry: IncubationEntry) {
        this._incubationEntry = incubationEntry;
        if(incubationEntry.is_in_progress) {
            const currentTime = Math.floor(Date.now() / 1000);
            if (currentTime > incubationEntry.maturation_at.T) {
                this.setState(IncubationSlotState.READY);
            } else {
                this.setState(IncubationSlotState.INCUBATION);
            }
        } else {
            this.setState(IncubationSlotState.EMPTY);
        }
    }

    //手动调用节点销毁时执行
    onDestroy(): void {
        this.stopIconBounceAnimation();
    }

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}