const { regClass, property } = Laya;

class ParticlePool<T extends Particle> {
    pool: T[] = [];
    maxSize: number;

    constructor(maxSize: number) {
        this.maxSize = maxSize;
    }

    // 根据粒子类型从池中获取粒子
    getParticle(x: number, y: number, texture: Laya.Texture, ParticleClass: new (...args: any[]) => T): T {
        let particle: T;
        if (this.pool.length > 0) {
            // 如果池中有粒子，从池中取出并重置
            particle = this.pool.pop()!;
            particle.reset(x, y, texture);
        } else {
            particle = new ParticleClass(x, y, texture);
        }
        return particle;
    }

    // 将粒子归还到池中
    returnParticle(particle: T): void {
        if (this.pool.length < this.maxSize) {
            this.pool.push(particle);
        }
    }
}

export abstract class Particle {
    x: number;
    y: number;
    vx: number;
    vy: number;

    life: number;
    fixedLife: number;
    minLife: number;
    maxLife: number;
    
    width: number;
    height:number;

    size: number;
    minSize: number;
    maxSize: number;
    alpha: number;
    texture: Laya.Texture;
    isLoop: boolean;          // 是否为循环粒子


    constructor(x: number, y: number, texture: Laya.Texture, isLoop: boolean) {
        this.reset(x, y, texture);
        this.isLoop = isLoop;
    }

    setSize(minSize: number, maxSize: number): void {
        this.minSize = minSize;
        this.maxSize = maxSize;
    }

    setLife(minLife: number, maxLife: number): void {
        this.minLife = minLife;
        this.maxLife = maxLife;
    }

    // 重置粒子的状态
    reset(x: number, y: number, texture: Laya.Texture): void {
        this.x = x;
        this.y = y;
        this.texture = texture;
        this.width = texture.width;
        this.height = texture.height;
    }

    // 更新粒子的状态
    abstract update(): void;

    // 判断粒子是否已过期
    isAlive(): boolean {
        return this.isLoop || (this.life > 0 && this.alpha > 0); // 循环粒子永远活着
    }

    // 绘制粒子
    draw(sprite: Laya.Sprite): void {
        sprite.graphics.drawTexture(
            this.texture,
            this.x - (this.width * this.size) / 2,
            this.y - (this.height * this.size) / 2,
            this.width * this.size,
            this.height * this.size,
            null,
            this.alpha
        );
    }
}

class DefaultParticle extends Particle {
    
    constructor(x: number, y: number, texture: Laya.Texture) {
        super(x, y, texture, true); // 爆炸粒子设置为一次性粒子
    }

    // 重置粒子的状态
    reset(x: number, y: number, texture: Laya.Texture): void {
        super.reset(x, y, texture);

        this.vx = Math.random() * 10 - 5;
        this.vy = Math.random() * 10 - 5;
        this.life = Math.random() * 100;
        this.size = 0.2 + Math.random() * 0.8;
        this.alpha = 1;
    }

    // 更新粒子的状态
    update(): void {
        this.x += this.vx;
        this.y += this.vy;
        this.life -= 1;
        this.alpha -= 0.01;
        //this.size -= 0.01;

        if (this.isLoop && this.life <= 0) {
            this.reset(0, 0, this.texture); // 重置为新的随机状态
        }
    }
}

export class ParticleSystem<T extends Particle> {
    particles: T[] = []; // 所有粒子
    pool: ParticlePool<T>;
    texture: Laya.Texture;
    maxParticles: number;

    constructor(texture: Laya.Texture, maxParticles: number) {
        this.texture = texture;
        this.maxParticles = maxParticles;
        this.pool = new ParticlePool(maxParticles);
    }

    // 发射粒子（一次性或循环）
    emit(x: number, y: number, ParticleClass: new (...args: any[]) => T, count: number = 1): void {
        for (let i = 0; i < count; i++) {
            if (this.particles.length < this.maxParticles) {
                const particle = this.pool.getParticle(x, y, this.texture, ParticleClass);
                this.particles.push(particle);
            }
        }
    }

    // 更新所有粒子
    update(): void {
        for (let i = this.particles.length - 1; i >= 0; i--) {
            this.updateParticle(this.particles[i], i);
        }
    }

    // 更新单个粒子
    private updateParticle(particle: T, index: number): void {
        particle.update();

        if (!particle.isAlive()) {
            this.pool.returnParticle(particle);
            this.particles.splice(index, 1);
        }
    }

    // 绘制所有活跃的粒子
    draw(sprite: Laya.Sprite): void {
        sprite.graphics.clear();
        this.particles.forEach(particle => particle.draw(sprite));
    }

    public isAllLive(): boolean{
        let isLive = false;
        for (let i = this.particles.length - 1; i >= 0; i--) {
            if (this.particles[i].isAlive()) {
                isLive = true;
                return isLive;
            }
        }

        return isLive;
    }
}

