const { regClass, property } = Laya;

@regClass('1ZbHXoulRJCgzOhnyG97lA')
export class WebMessage extends Laya.Script {

    onStart() {
        console.log("Game start");
        

        window.addEventListener('message', (event: MessageEvent) => {  
            // 检查消息的来源，确保它来自你的React应用  
            // event.origin 应该是你的React应用所在的域  
            // if (event.origin !== 'http://your-react-app-origin.com') {  
            //   return;  
            // }  
            
            // 处理接收到的消息  
            const message = event.data;  
            console.log('Received message from parent:', message);  
            
            // 根据消息类型执行相应的操作  
            if (message.type === 'REACT_MESSAGE') {  
              // 处理来自React的消息  
              //const message2 = { type: 'GAME_LOG', data: { "GAME_LOG":message } }; 
              //WebMessage.sendMessageToWeb(message2);
            }  
        });

        //const message = { type: 'GAME_EVENT', data: { "123123123":123123123 } }; 
        //WebMessage.sendMessageToWeb(message);
    }

    public static sendMessageToWeb(message: any) {  
        // 检查父窗口是否存在，并且不是当前窗口自身  
        if (window.parent !== window && window.parent.postMessage) {  
          // 使用 postMessage 发送消息到父窗口  
          // 第二个参数是目标源，通常设置为 '*' 表示不限制源，但在生产环境中应该设置为允许的源列表  
          window.parent.postMessage(message, '*');  
        }  
    }
}