import { UIBase } from "../UIBase";

export class UIMessageBoxOne extends UIBase {

    private _textMessage : fgui.GTextField;

    public initUI(): void {
        super.initUI();

        this._textMessage = this.view.getChild("TEXT_Message").asTextField;
    }

    public didOpen(): void {

        super.didOpen();
        
        this._textMessage.text = this.param.content;
    }
}