import { SystemDefine } from "../SystemDefine";
import { UIMessageBoxOne } from "./HUD/UIMessageBoxOne";
import { UITips } from "./HUD/UITips";
import { UIBase } from "./UIBase";
import { UIClassName, UIDefine } from "./UIDefine";

const { regClass, property } = Laya;

interface UIQueueBase {
    name: string;
    data: any;
} 

@regClass('G_pKT3cPTh-KsbcmdsUFMg')
export class UIManager extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    //declare owner : Laya.Sprite;
    public ownerPanel: Laya.Box;

    private static _instance: UIManager;  
    
    private _uiConfigData: any;
    private _viewsDictionary: { [key: string]: fgui.GComponent } = {}; 
    private _uibaseDictionary: { [key: string]: UIBase } = {}; 
    private _uiDefine: UIDefine
    private _initialized: boolean = false;
    private _isBackgroundOpen: number = 0;
    public pageWidth: number;
    public pageHeight: number;

    // 队列用于管理打开的 UI
    private _uiQueue: UIQueueBase[] = [];
    private _tipsQueue: UIQueueBase[] = [];

    constructor() {  
        super();

        this.pageWidth = Laya.stage.designHeight * Laya.Browser.clientWidth / Laya.Browser.clientHeight;
        this.pageHeight = Laya.stage.designHeight;
    }

    public static get instance(): UIManager {  
        if (!UIManager._instance) {  
            return null;
        }  
        return UIManager._instance;  
    }  

    public get initialized(): boolean {
        return this._initialized;
    }

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    onAwake(): void {
        if (UIManager._instance) {  
            throw new Error("Singleton class. Use getInstance() instead.");
        }  

        UIManager._instance = this
        this.ownerPanel = this.owner as Laya.Panel; 
    }

    init(): void {
        this._uiDefine = new UIDefine();

        this.initFont();
        
        Laya.loader.load("resources/prefabs/ui/P_UIRoot.lh").then(res => {
            let sprite : Laya.Sprite = res.create();
            this.ownerPanel.addChild(sprite);

            sprite.addChild(fgui.GRoot.inst.displayObject);
            sprite.zOrder = 100;
            sprite.width = this.pageWidth;
            //console.log("sprite.width = " + sprite.width);

            Laya.loader.load("resources/json/Data_UI_Config.json").then(res => {
                
                //const uiConfigDataRes = Laya.loader.getRes("resources/json/Data_UI_Config.json");
                this._uiConfigData = res.data;
                let packageList: string[] = [];
                for (let i = 0; i < this._uiConfigData.packageConfigs.length; i++) {  
                    const packageName = this._uiConfigData.packageConfigs[i];  
                    packageList.push(this._uiConfigData.uiBasePath + packageName);
                }
    
                fgui.UIPackage.loadPackage(packageList, Laya.Handler.create(this, this.onUILoaded));
            });

        });  
    }

    initFont(): void {
        fairygui.UIConfig.defaultFont = "TitanOne-Regular";
        Laya.loader.load(`resources/font/TitanOne-Regular.ttf`, Laya.Handler.create(this, (ttf: { [x: string]: any; }) => {
            fairygui.UIConfig.defaultFont = ttf['fontName'];
            Laya.Text.defaultFont = ttf['fontName'];
        }), null, Laya.Loader.TTF);
    }

    onUILoaded() {
        
        for (let i = 0; i < this._uiConfigData.uiConfigs.length; i++) {  
            const config = this._uiConfigData.uiConfigs[i];  
            let view = fgui.UIPackage.createObject(config.uiPackage, config.uiName).asCom;
            view.makeFullScreen();
            //view.visible = false;
            view.sortingOrder = config.layer;
            fgui.GRoot.inst.addChild(view);
            
            let uiClass = this._uiDefine.instantiate(config.uiName)
            if (uiClass) {
                let component = view.displayObject.addComponentInstance(uiClass);
                if(!component) {
                    console.error("Component not found: " + config.uiName);
                }
                uiClass.uiManager = this;
                uiClass.view = view;
                uiClass.config = config;
                uiClass.initUI();
                uiClass.forceClose();
                this._uibaseDictionary[config.uiName] = uiClass;
            } else {
                console.error("UIBase not found: " + config.uiName);
            }
            
            this._viewsDictionary[config.uiName] = view;
        }

        this._initialized = true;
    }


    public pushUI(name : string, data: any): void
    {
        if (this._uibaseDictionary[name]) {

            if (this._uiQueue.length > 0) {
                const lastUI = this._uiQueue[this._uiQueue.length - 1];
                this.forceCloseUI(lastUI.name);
            }

            this._uiQueue.push({
                name: name,
                data: data
            });

            this.openUI(name, data);
        }
    }

    public popLastUI(): void
    {
        if (this._uiQueue.length > 0) {
            const lastUI = this._uiQueue.pop();
        
            //在移除一个之后还有一个的话
            if (this._uiQueue.length > 0) {
                this.forceCloseUI(lastUI.name);

                const nextUI = this._uiQueue[this._uiQueue.length - 1];
                this.openUI(nextUI.name, nextUI.data);
            } else {
                this.closeUI(lastUI.name);
            }
        }
    }

    public pushTips(name : string, data: any): void
    {
        if (this._uibaseDictionary[name]) {

            if (this._tipsQueue.length > 0) {
                const lastUI = this._tipsQueue[this._tipsQueue.length - 1];
                this.forceCloseUI(lastUI.name);
            }

            this._tipsQueue.push({
                name: name,
                data: data
            });

            this.openUI(name, data);
        }
    }

    public popLastTips(): void
    {
        if (this._tipsQueue.length > 0) {
            const lastUI = this._tipsQueue.pop();

            //在移除一个之后还有一个的话
            if (this._tipsQueue.length > 0) {
                this.forceCloseUI(lastUI.name); 

                const nextUI = this._tipsQueue[this._tipsQueue.length - 1];
                this.openUI(nextUI.name, nextUI.data);
            } else {
                this.closeUI(lastUI.name); 
            }
        }
    }

    //外部使用push和pop不要用open和close
    private openUI(name : string, data: any) : UIBase{
        if (this._uibaseDictionary[name]) {  
            this._uibaseDictionary[name].open(data);
            return this._uibaseDictionary[name];
        } else {  
            console.error("View not found: " + name);  
            return null;
        }
    }

    //外部使用push和pop不要用open和close
    private closeUI(name : string) : void{
        if (this._uibaseDictionary[name]) {  
            this._uibaseDictionary[name].close();  
        } else {  
            console.error("View not found: " + name);  
        }  
    }

    private forceCloseUI(name : string) : void{
        if (this._uibaseDictionary[name]) {  
            this._uibaseDictionary[name].forceClose();  
        } else {  
            console.error("View not found: " + name);  
        }  
    }

    public isOpenedUI(name : string) : boolean{
        if (this._uibaseDictionary[name]) {  
            return this._uibaseDictionary[name].isOpened();  
        } else {  
            console.error("View not found: " + name);  
            return false;
        }  
    }

    public openBackground() : void{
        
        this._isBackgroundOpen++;

        //console.log("this._isBackgroundOpen = " + this._isBackgroundOpen);

        this.openUI("UI_Background_HUD", null);
    }

    public closeBackground() : void {

        this._isBackgroundOpen--;

        //console.log("this._isBackgroundClose = " + this._isBackgroundOpen);

        if(this._isBackgroundOpen <= 0)
        {
            this._isBackgroundOpen = 0;
            this.closeUI("UI_Background_HUD");
        }
    }

    public forceCloseBackground() : void {
        this._isBackgroundOpen = 0;
        this.forceCloseUI("UI_Background_HUD");
    }

    public ShowTips(content: string)
    {   
        this.pushTips(UIClassName.UI_Tips_HUD, {
            content: content
        });
    } 

    public ShowMessageBox(content: string, callback: Function) : void{
        this.pushUI(UIClassName.UI_MessageBoxOne_HUD, {
            content: content,
            callback: callback
        });
    }

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}