import { UIBase } from "../UIBase";
import Tween = Laya.Tween;
import Ease = Laya.Ease;
import Handler = Laya.Handler;

export class UITips extends UIBase {
    private _textMessage : fgui.GTextField;
    private _originY: number = 0;

    public initUI() : void{

        super.initUI();
        
        this._textMessage = this.view.getChild("TEXT_Message").asTextField;
        this._originY = this.view.displayObject.y;
        
    }

    // public setMessage(message : string) : void {
    //     this._textMessage.text = message;u
    // }

    public didClose(): void {
        super.didClose();
        Tween.clearAll(this.view.displayObject);
        Laya.timer.clearAll(this);
    }

    public didOpen(): void {

        super.didOpen();
        
        this._textMessage.text = this.param.content;

        this.view.displayObject.y = this._originY;
        Tween.from(this.view.displayObject, {"y": -100}, 600, Ease.backOut, Handler.create(this, () => {  

            Laya.timer.once(2000, this, () => {  

                Tween.to(this.view.displayObject, {"y": -100}, 600, Ease.backIn, Handler.create(this, () => {  

                    this.popTips();
                
                }));
            
            });

        }));
    }
}