import { UIManager } from "../UI/UIManager";
import { GameStateBase } from "./GameStateBase";
import { GameState, GameStateManager } from "./GameStateManager";
import { HttpManager, postWithPromise, getWithPromise } from "../Http/HttpManager";
import { ConfigManager } from "../Config/ConfigManager";
import { RunTimeDataManager } from "../RunTimeData/RunTimeDataManager";
import { HttpURL } from "../Http/HttpDefine";
import { Splash } from "../Component/Splash";

// 定义生成器函数  
async function* processSteps() {  

    while(!ConfigManager.instance) {
       await new Promise(resolve => setTimeout(resolve, 100));
        //yield "Checking ConfigManager initialization..."; 
    }

    ConfigManager.instance.loadAll();
    while (!ConfigManager.instance.initialized) {
       await new Promise(resolve => setTimeout(resolve, 100));
        //yield "Checking loaded..."; 
    }

    while (!UIManager.instance) {  
       await new Promise(resolve => setTimeout(resolve, 100));  
        //yield "Checking UIManager initialization...";  
    }  

    UIManager.instance.init();
    while (!UIManager.instance.initialized) {  
       await new Promise(resolve => setTimeout(resolve, 100));  
        //yield "Checking initialization...";  
    }  

    while (!HttpManager.instance) {  
       await new Promise(resolve => setTimeout(resolve, 100));  
        //yield "Checking HttpManager initialization...";  
    } 
  
    while(!RunTimeDataManager.instance) {
        await new Promise(resolve => setTimeout(resolve, 100));
        //yield "Checking RunTimeDataManager initialization..."; 
    }


    //等待tg给与用户登录字符串
    try {  
        //登录
        const loginData = { 
            init_data_raw: "query_id=AAFV-CoDAQAAAFX4KgODJr1_&user=%7B%22id%22%3A100000003%2C%22first_name%22%3A%22NNNNiko%22%2C%22last_name%22%3A%22Test%22%2C%22username%22%3A%22niko_test1%22%2C%22language_code%22%3A%22zh-hans%22%2C%22allows_write_to_pm%22%3Atrue%7D&auth_date=1722587152&hash=8e791a997e55723714f73878fdeb27d7e17157f5fb2b460d6fc461f5ce82809c", 
            //inviter_user: 30 
        };
        
        await postWithPromise(HttpManager.instance, HttpURL.signup_or_login, loginData);  

        await getWithPromise(HttpManager.instance, HttpURL.login_data_update, "");  

        //默认写死的,后面更新
        const getMachineData = { 
            machine_id: "ID_1000",
        }; 
        //获取娃娃机的信息
        await postWithPromise(HttpManager.instance, HttpURL.update_machine_data, getMachineData);  

        GameStateManager.instance.changeState(GameState.MAINMENU);  
        yield "StateChanged to MAINMENU";  

    } catch (error) {  
        // 请求失败，处理错误  
        console.error("HTTP request failed:", error);  
        yield "HTTP request failed"; // 可选：返回错误状态信息  
    }
}  

export class LoadingState extends GameStateBase {
    enter(): void {
        Splash.openSplash();

        console.log("Entering LoadingState");
        (async () => {  
            const gen = processSteps();  
            for await (const message of gen) {  
                console.log(message); // 输出生成器返回的状态信息  
            }  
        })();

    }

    exit(): void {
        console.log("Exiting LoadingState");

        Splash.closeSplash();
    }

    update(): void {
        // if(UIManager.instance !== null) {
        //     if(UIManager.instance.initialized) {
        //         this.gameStateManager.changeState(GameState.MAINMENU);
        //     }
        // }
    }
}