
import { SoundFXManager } from "../../Component/SoundManager";
import { ConfigHelper } from "../../Config/ConfigManager";
import { MachineConfig } from "../../Config/ExcelMachine";
import { HttpURL } from "../../Http/HttpDefine";
import { HttpManager } from "../../Http/HttpManager";
import { RunTimeDataManager } from "../../RunTimeData/RunTimeDataManager";
import { UIBase } from "../UIBase";
import { UIManager } from "../UIManager";

export class UIUnlockEggIncuabtion extends UIBase {
    
    private _bPointUnlock : fgui.GButton;
    private _bGemUnlock : fgui.GButton;
    
    public initUI(): void {
        super.initUI();

        this._bPointUnlock = this.view.getChild("B_PointUnlock").asButton;
        this._bPointUnlock.onClick(this, ()=>{
            this.onUnlock();
        });

        this._bGemUnlock = this.view.getChild("B_GemUnlock").asButton;
        this._bGemUnlock.onClick(this, ()=>{
            this.onUnlock();
        });   
    }

    public onUnlock(){

        const machineData = RunTimeDataManager.instance.runTimeData.machine_data
        //发送网络消息
        const params = {
            "machine_id": machineData.id,
        }

        HttpManager.instance.post(HttpURL.unlock_egg_incubation, params, (response: any) => {  

            RunTimeDataManager.instance.machine.updateDisplay();

            UIManager.instance.popLastUI();
            UIManager.instance.ShowMessageBox("successfully!", null);
            SoundFXManager.playSoundFX("SFX_UnlockEggIncuabtion");
            //this.updateDisplay();
            //这里要打开一个收获页面
            //this.handleHarvestEgg(response, worldPos.x, worldPos.y);

        }, (error: any) => {  
            UIManager.instance.ShowMessageBox(error, null);
        }); 
    }

    public didOpen(): void {
        super.didOpen();

        const machineData = RunTimeDataManager.instance.runTimeData.machine_data;
        const nowSlotCount = machineData.getIncubationDataCount();
        
        const machineConfig = MachineConfig.getConfig(machineData.id)
        const unlockIncubatorCost = ConfigHelper.parseStringArray(machineConfig.UnlockIncubatorCost);
        const symbol = unlockIncubatorCost[nowSlotCount * 2]
        const cost = unlockIncubatorCost[nowSlotCount * 2 + 1]

        switch (symbol) {
            case "point":
                this._bPointUnlock.visible = true;
                this._bGemUnlock.visible = false;
                this._bPointUnlock.getChild("title0").asTextField.text = cost;
                this._bPointUnlock.getChild("title1").asTextField.text = cost;
                break;
            case "gem":
                this._bPointUnlock.visible = false;
                this._bGemUnlock.visible = true;
                this._bGemUnlock.getChild("title0").asTextField.text = cost;
                this._bGemUnlock.getChild("title1").asTextField.text = cost;
                break;
        }

        

    }
}