const { regClass, property } = Laya;

@regClass('6e9BXDZiQn-RSSxV5cRK1g')
export class Arrow extends Laya.Script {
    declare owner: Laya.Sprite;

    // 移动速度
    private moveSpeed: number = 2; 
    // 移动方向，1 表示向上，-1 表示向下
    private direction: number = 1; 
    // 最大和最小 Y 坐标
    private minY: number;
    private maxY: number;

    // 组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    onAwake(): void {
        this.minY = this.owner.y - 20; // 根据需要设置最小 Y 坐标
        this.maxY = this.owner.y + 20; // 根据需要设置最大 Y 坐标
    }

    // 每帧更新时执行
    onUpdate(): void {
        // 更新箭头的 Y 坐标
        this.owner.y += this.moveSpeed * this.direction;

        // 检查边界
        if (this.owner.y >= this.maxY) {
            this.direction = -1; // 到达上边界后，改变方向
        } else if (this.owner.y <= this.minY) {
            this.direction = 1; // 到达下边界后，改变方向
        }
    }
}
