import { CountdownTimer } from "../../Component/CountdownTimer";
import { SoundFXManager } from "../../Component/SoundManager";
import { EggConfig } from "../../Config/ExcelEgg";
import { Egg } from "../../GameLogic/Egg";
import { IncubationEntry } from "../../RunTimeData/RunTimeData";
import { RunTimeDataManager } from "../../RunTimeData/RunTimeDataManager";
import { UIBase } from "../UIBase";

export class UIShowEggIncuabtion extends UIBase {
    
    private _imgEggRoot : fgui.GGraph;
    private _eggSprite: Laya.Sprite;
    private _pbEggIncuabtionProgress: fgui.GProgressBar;

    private _iconScale = 0;
    private _iconOriginY = 0;
    private _eggConfig: EggConfig;

    private _textCooldownTime: fgui.GTextField;
    private _countdownTimer : CountdownTimer;

    private _incubationData: IncubationEntry;

    public initUI(): void {
        super.initUI();
        
        this._textCooldownTime = this.view.getChild("TEXT_CooldownTime").asTextField;

        this._imgEggRoot = this.view.getChild("IMG_EggRoot").asGraph;
        this._pbEggIncuabtionProgress = this.view.getChild("PB_Eggincuabtion").asProgress;
    }

    public didOpen(): void {
        this.setIncubationIndex(this.param.incubationIndex)
    }

    public setIncubationIndex(index: number): void {
        const machineData = RunTimeDataManager.instance.runTimeData.machine_data;
        if(machineData.incubation_list.length <= 0) {
            this.pop(); return;
        }

        this._incubationData = machineData.incubation_list[index];
        this.initIcon(this._incubationData);
        this.initTimeShower();
        
    }

    private clear(): void {

        if(this._countdownTimer != null) {
            this._countdownTimer.stop();
            this._countdownTimer = null;
        }

        this._textCooldownTime.text = "";

        // 停止跳动动画
        this.stopIconBounceAnimation();

        if (this._eggSprite) {
            this._eggSprite.destroy();
            this._eggSprite = null;
        }

        this._incubationData = null;
    }

    public didClose(): void {
        super.didClose();
        this.clear();
    }

    private initIcon(entry: IncubationEntry) {
        
        this._eggConfig = EggConfig.getConfig(entry.config_id);
        Laya.loader.load(this._eggConfig.Prefab).then(res => {
            let sprite: Laya.Sprite = res.create();
            sprite.anchorX = 0.5;
            sprite.anchorY = 1;

            this._imgEggRoot.displayObject.addChild(sprite);

            // 获取 icon 和 sprite 的宽高
            let aWidth = this._imgEggRoot.displayObject.width;
            let aHeight = this._imgEggRoot.displayObject.height;

            let bWidth = sprite.width * 1.2;
            let bHeight = sprite.height * 1.2;

            // 计算宽高比例
            let scaleX = aWidth / bWidth;
            let scaleY = aHeight / bHeight;

            // 取最小的比例来保证等比缩放
            let scale = Math.min(scaleX, scaleY);

            // 应用等比缩放
            sprite.scale(scale, scale, true);

            // 将 sprite 放置在 icon 的中心位置
            sprite.pos(aWidth / 2, aHeight - 30);

            //sprite.anchorY = 1;

            const egg = sprite.getComponent(Egg);
            egg.removeRigidBody();
            
            egg.eggID = entry.egg_id;
            egg.init();

            this._eggSprite = sprite;
            this._iconScale = this._eggSprite.scaleY;
            this._iconOriginY = this._eggSprite.y;
            
            this._pbEggIncuabtionProgress.min = 0;
            this._pbEggIncuabtionProgress.max = this._eggConfig.IncubationTime;
            this.startIconBounceAnimation();

            SoundFXManager.playSoundFX(this._eggConfig.Sfx);
        }); 
    }

    // 停止跳动动画
    private stopIconBounceAnimation() {
        if (this._eggSprite) {
            Laya.Tween.clearTween(this._eggSprite); // 清除 this.icon 上的所有动画
        }
    }

    // 给icon增加一个跳动的动画效果
    private startIconBounceAnimation() {
        // 首先停止之前可能存在的动画
        this.stopIconBounceAnimation(); 

        // 动画函数：让icon缩放变大并稍微上移，带有弹力效果
        const bounce1 = () => {
            Laya.Tween.to(this._eggSprite, { scaleX: this._iconScale * 1.1, scaleY: this._iconScale * 0.8 }, 1000, Laya.Ease.linearInOut, Laya.Handler.create(this, bounce2));
        };

        // 动画函数：让icon缩放变小并回到原位置，带有弹力效果
        const bounce2 = () => {
            Laya.Tween.to(this._eggSprite, { scaleX: this._iconScale, scaleY: this._iconScale }, 1000, Laya.Ease.linearInOut, Laya.Handler.create(this, bounce1));
        };

        // 开始第一次动画
        bounce1();
    }

    initTimeShower() {

        if(this._countdownTimer != null) {
            this._countdownTimer.restart(this._incubationData.maturation_at.T)
            return;
        }

        this._countdownTimer = new CountdownTimer(this._incubationData.maturation_at.T, 
            (remainingTime, difference) => {
                this._textCooldownTime.text = `${remainingTime}`
                this._pbEggIncuabtionProgress.value = difference;
            },
            (remainingTime, difference) => {
                this._textCooldownTime.text = `${remainingTime}`
                this._pbEggIncuabtionProgress.value = difference;
            },
            () => {
                this.pop();
            }
        );

        this._countdownTimer.start();
    }
}