import { UIBase } from "../UIBase";

export class UIBackground extends UIBase {

    public initUI(): void {
        super.initUI();
        
        let bBackgourndCloseButton = this.view.getChild("B_BackgroundCloseForBlackBG");
        if(bBackgourndCloseButton) {
            bBackgourndCloseButton.onClick(this, this.closeBackground);
        }
    }

    public closeBackground() : void{

        //一般需要调用这个地方肯定是已经出错了,就这样处理了
        this.uiManager.forceCloseBackground();
    }
}