import { RunTimeDataManager } from "../RunTimeData/RunTimeDataManager";
import BoxCollider = Laya.BoxCollider;
import Sprite = Laya.Sprite;
import Event = Laya.Event;
import RigidBody = Laya.RigidBody;
import ChainCollider = Laya.ChainCollider;
import MouseJoint = Laya.MouseJoint;
import PolygonCollider = Laya.PolygonCollider;
import CircleCollider = Laya.CircleCollider;
import RevoluteJoint = Laya.RevoluteJoint;

import { EggConfig } from "../Config/ExcelEgg";
import { Egg } from "./Egg";

const { regClass, property } = Laya;

@regClass('DVR1zPUiRceIY6FayhcJTg')
export class MachinePhysics extends Laya.Script {

    declare owner : Laya.Sprite;

    public box2D: Laya.Box;

    public pageWidth: number;
    public pageHeight: number;

    public static k_smallGroup = 1;
    public static k_middleGroup = 0;
    public static k_largeGroup = -1;
    public static k_triangleCategory = 0x2;
    public static k_boxCategory = 0x4;
    public static k_circleCategory = 0x8;
    public static k_triangleMask = 0xF;
    public static k_boxMask = 0xF ^ MachinePhysics.k_circleCategory;
    public static k_circleMask = 0xF; // 0x01为house刚体默认的category，若不设置，则会穿透house

    private _eggMap: Record<string, Egg>;

    constructor() {
        super();

        this.pageWidth = Laya.stage.designHeight * Laya.Browser.clientWidth / Laya.Browser.clientHeight;
        this.pageHeight = Laya.stage.designHeight;

        this._eggMap = {}
    }

    onStart(): void {

        //super.base();
        this.box2D = this.owner as Laya.Panel;
        //this.box2D.bgColor = "#232628";
        Laya.Config.isAntialias = true;
        Laya.Physics2D.I.start();
        this.createHouse();
        
        this.createEggs();
    } 

    
    createHouse() {
        let house = new Sprite();
        //@ts-ignore
        //console.log(house.globalRotation)
        this.box2D.addChild(house);
        let rigidbody: RigidBody = house.addComponent(RigidBody);
        rigidbody.type = "static";
        let chainCollider: ChainCollider = house.addComponent(ChainCollider);
        chainCollider.loop = true;

        let offsetWidth = 30;
        let offsetHeight = -10;

        chainCollider.datas = [
            0 + offsetWidth, 0, 
            0 + offsetWidth, this.owner.height - offsetHeight, 
            this.owner.width - offsetWidth, this.owner.height - offsetHeight, 
            this.owner.width - offsetWidth, 0];
    }

    addGroup(rigidbody: Laya.RigidBody, ratio: number) {
        switch (ratio) {
            case 1:
                rigidbody.group = MachinePhysics.k_smallGroup;
                break;
            case 2:
                rigidbody.group = MachinePhysics.k_middleGroup;
                break;
            case 3:
                rigidbody.group = MachinePhysics.k_largeGroup;
                break;
        }
    }

    createEggs() {
        
        //测试数据
        // Laya.loader.load("resources/prefabs/egg/P_10002001.lh").then(res => {
        //     for (let i = 1; i <= 10; i++){
        //         let randomNumber = 0.8;// + (Math.random() * 0.5)
        //         let sprite : Laya.Sprite = res.create();
        //         sprite.scale(randomNumber, randomNumber , true);
        //         sprite.pos(100, 100)
        //         this.box2D.addChild(sprite); 
        //         let rigidbody: RigidBody = sprite.getComponent(RigidBody);
        //         rigidbody.category = MachinePhysics.k_triangleCategory;
        //         rigidbody.mask = MachinePhysics.k_triangleMask;
        //         rigidbody.group = 1;
        //     }
        // });

        //根据网络数据加载蛋
        const machineData = RunTimeDataManager.instance.runTimeData.machine_data;
        let offsetWidth = 50;
        let offsetHeight = 300;

        Object.keys(machineData.egg_list).forEach(eggID => {
            const eggData = machineData.egg_list[eggID];
            const eggConfig = EggConfig.getConfig(eggData.config_id);

            Laya.loader.load(eggConfig.Prefab).then(res => {
                let randomNumber = eggConfig.Scale;
                let sprite : Laya.Sprite = res.create();
                sprite.scale(randomNumber, randomNumber, true);
                sprite.pos(offsetWidth + ((this.owner.width - offsetWidth) * Math.random()), offsetHeight * Math.random())
                this.box2D.addChild(sprite);
                const rigidBody = sprite.getComponent(RigidBody);
                rigidBody.category = MachinePhysics.k_triangleCategory;
                rigidBody.mask = MachinePhysics.k_triangleMask;
                rigidBody.group = 1;

                const egg = sprite.getComponent(Egg);
                egg.eggID = eggID;
                egg.machinePhysics = this;
                egg.init();
                
                this._eggMap[eggID] = egg;
            }); 

            //console.log(`Egg ID: ${eggId}, Config ID: ${eggData.config_id}`);
        });
    }

    public removeEgg(eggID: string) {
        if(this._eggMap[eggID]) {
            this._eggMap[eggID].owner.destroy();
            delete this._eggMap[eggID];
        }
    }

    public getEgg(eggID: string) : Egg {
        return this._eggMap[eggID];
    }

    public removeAllEggs() {
        Object.keys(this._eggMap).forEach(eggID => {
            this.removeEgg(eggID);
        })
    }

    public clearAllEggs() {
        Object.keys(this._eggMap).forEach(eggID => {
            this.getEgg(eggID).clear();
            delete this._eggMap[eggID];
        })
    }

    onDisable(): void {
        Laya.Physics2D.I.destroyWorld();
    }
}