const { regClass, property } = Laya;

import { Egg } from "./Egg";
import { Machine } from "./Machine";
import Animator2D = Laya.Animator2D;
import Sprite = Laya.Sprite;
import Event = Laya.Event;
import ColliderBase = Laya.ColliderBase;
import RigidBody = Laya.RigidBody;
import Tween = Laya.Tween;
import Ease = Laya.Ease;
import Handler = Laya.Handler;
import WeldJoint = Laya.WeldJoint;
import MathUtil = Laya.MathUtil;
import Point = Laya.Point;
import Label = Laya.Label;
import { RunTimeDataManager } from "../RunTimeData/RunTimeDataManager";
import { SoundFXManager } from "../Component/SoundManager";
import { MathHelper } from "../Utils/MathHelper";

enum ClawState {
    None,
    Idle,//待机,可移动
    Dropping,//下落
    DroppingStop,//下落停止
    Grabbing,//抓取
    Pulling,//拉起
}

// 定义生成器函数  
async function* processGrabSteps(claw:Claw) {  

    //下落
    claw.changeState(ClawState.Dropping)
    
    while (!claw.clawFindedEgg) {  
        // 等待一段时间后再检查  
        await new Promise(resolve => setTimeout(resolve, 100));  
        //yield "Checking claw.clawFindedEgg...";
    }

    //下落停止
    claw.changeState(ClawState.DroppingStop)
    claw.waittingTime = 0;
    while (claw.waittingTime < 200) {  
        await new Promise(resolve => setTimeout(resolve, 100));
        //yield "Checking claw.clawFindedEgg...";
        claw.waittingTime += 100;
    }

    //抓取
    claw.changeState(ClawState.Grabbing)

    claw.waittingTime = 0;
    while (claw.waittingTime < 1000) {  
        await new Promise(resolve => setTimeout(resolve, 100));
        //yield "Checking claw.clawFindedEgg...";
        claw.waittingTime += 100;
    }
    
    //拉起
    claw.changeState(ClawState.Pulling)
    
    claw.waittingTime = 0;
    while (claw.waittingTime < 3000) {  
        await new Promise(resolve => setTimeout(resolve, 100));
        //yield "Checking claw.clawFindedEgg...";
        claw.waittingTime += 100;
    }

    claw.changeState(ClawState.Idle)
}  


@regClass('pneh_T95RWiOY4p54TqJ9g')
export class Claw extends Laya.Script {
    //declare owner : Sprite3D;
    declare owner : Sprite;

    @property(Sprite)
    public clawLeft : Sprite;

    @property(Sprite)
    public clawRight : Sprite;

    @property( { type:Animator2D } ) //拿到 Animation 组件
    public animator: Animator2D;

    @property(Sprite)
    public intersectionTestBox: Sprite;

    @property(Sprite)
    public pickupTestBox: Sprite;

    @property(RigidBody)
    public pickupTestRigidBody: RigidBody;

    @property(Sprite)
    public lineSprite: Sprite;

    @property(Sprite)
    public baseSprite: Sprite;

    @property(Label)
    public textGameCoin: Label;

    //找到蛋之后的反馈
    public onFoundEggCallback: (eggID: string) => void;

    public clawFindedEgg : boolean;
    public waittingTime: number;
    public downTween: Tween;
    public pingpongTween: Tween;
    public findedEgg: Egg;
    public curEgg: Egg;
    
    public isPicking = false;

    private _clawState:ClawState = ClawState.None;
    private _positionY: number;
    private _lineOrignalHeight: number;

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    //onAwake(): void {}

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    onDisable(): void {
        this.intersectionTestBox.off(Event.TRIGGER_ENTER, this, this.onIntersectionTestBoxTriggerEnter);
        this.pickupTestBox.off(Event.TRIGGER_ENTER, this, this.onPickupTestBoxTriggerEnter);
    }

    //第一次执行update之前执行，只会执行一次
    onStart(): void {
        //console.log("Claw");
        //this.animator.play("A_Claw_Open");

        this.intersectionTestBox.on(Event.TRIGGER_ENTER, this, this.onIntersectionTestBoxTriggerEnter);
        this.pickupTestBox.on(Event.TRIGGER_ENTER, this, this.onPickupTestBoxTriggerEnter);

        this._positionY = this.baseSprite.y;
        this._lineOrignalHeight = this.lineSprite.height;
        //这个设置只是为了激活物理
        this.owner.x = 20;

        //这个勾爪左右的逻辑,这个可以专门做一个机器
        this.pingpong();

        this.changeState(ClawState.Idle);
    }

    onIntersectionTestBoxTriggerEnter(colliderB: ColliderBase, colliderA: ColliderBase, contact: any) {
        // let bodyB: RigidBody = colliderB.owner.getComponent(RigidBody);        
        // console.log("TestBoxTriggerEnter bodyB:", bodyB);

        // let bodyA: RigidBody = colliderA.owner.getComponent(RigidBody);
        // console.log("TestBoxTriggerEnter bodyA:", bodyA);

        this.clawFindedEgg = true;
    }

    onPickupTestBoxTriggerEnter(colliderB: ColliderBase, colliderA: ColliderBase, contact: any) {

        if(this.findedEgg == null && colliderB.label == "egg" && this._clawState < ClawState.Pulling)
        {
            let eggB:Egg = colliderB.owner.getComponent(Egg);
            //console.log("Pickup bodyB:", eggB);
            this.findedEgg = eggB;
        }
    }

    public updateGameCoinDisplay() {
        const coinData = RunTimeDataManager.instance.runTimeData.machine_coin_data;
        this.textGameCoin.text = coinData.getTotalGameCoin().toString();
        if (coinData.getTotalGameCoin() <= 0) {
            this.textGameCoin.color = "#FF6480";
        } else {
            this.textGameCoin.color = "#FFFFFF";
        }
    }

    public changeState(state:ClawState) : void {
        this._clawState = state;
        switch (state) {
            case ClawState.Idle:
                this.playAnimation("A_Claw_Idle");
                this.isPicking = false;
                this.pingpongTween.resume();
                break;
            case ClawState.Dropping:
                this.pingpongTween.pause();
                this.playAnimation("A_Claw_Open");
                this.findedEgg = null;
                this.curEgg = null;
                this.moveYDown();
                SoundFXManager.playSoundFX("SFX_ClawDropping");
                
                const randomNumber = MathHelper.randomRange(1, 2);
                SoundFXManager.playSoundFX(`SFX_Voice0${randomNumber}`);
                break;
            case ClawState.DroppingStop:
                this.downTween.clear();
                this.downTween = null;
                this.playAnimation("A_Claw_DroppingStop");
                break;
            case ClawState.Grabbing:
                SoundFXManager.playSoundFX("SFX_ClawGrabbing");
                this.playAnimation("A_Claw_Close");
                this.connectEgg();
                break;
            case ClawState.Pulling:
                this.moveYUp();
                this.playAnimation("A_Claw_Idle");
                //this.animator.play("A_Claw_Idle");
                break;
        }
    }

    public connectEgg(): void {
        if(this.findedEgg != null) {
            this.curEgg = this.findedEgg;
            this.findedEgg = null;
            
            let pickupTestWorldPos: Point = Point.create();
            pickupTestWorldPos.x = this.pickupTestBox.width * this.pickupTestBox.anchorX;
            pickupTestWorldPos.y = this.pickupTestBox.height * this.pickupTestBox.anchorY;
            pickupTestWorldPos = this.pickupTestBox.localToGlobal(pickupTestWorldPos);

            let eggWorldPos: Point = Point.create();
            eggWorldPos.x = this.curEgg.owner.width * this.curEgg.owner.anchorX;
            eggWorldPos.y = this.curEgg.owner.height * this.curEgg.owner.anchorY;
            eggWorldPos = this.curEgg.owner.localToGlobal(eggWorldPos);

            let finalPos: Point = Point.create();
            finalPos.x = this.curEgg.owner.x + (pickupTestWorldPos.x - eggWorldPos.x);
            finalPos.y = this.curEgg.owner.y + (pickupTestWorldPos.y - eggWorldPos.y);

            Tween.to(this.curEgg.owner, {"x": finalPos.x, "y": finalPos.y}, 200, Ease.linearNone, Handler.create(this, () => {  
                this.curEgg.buildJoint(this.pickupTestRigidBody);
            }));
        }
    }


    public grab(): void {
        
        if(this.isPicking) {
            return;
        }
        
        this.clawFindedEgg = false;
        this.isPicking = true;

        (async () => {  
            const gen = processGrabSteps(this);  
            for await (const message of gen) {  
                console.log(message); // 输出生成器返回的状态信息  
            }  
        })();
    }

    public playAnimation(name:string): void {
       // this.animator.crossFade(name, 0.5);
        this.animator.play(name);
    }

    public moveYDown(): void {
        this.downTween = Tween.to(this.baseSprite, { "y": 1100 }, 2000, Ease.linearNone, Handler.create(this, () => {  
            this.clawFindedEgg = true;
        }));
	} 

    public pingpong(): void {
        this.pingpongTween = Tween.to(this.owner, { "x": 680 }, 2000, Ease.linearNone, Handler.create(this, () => {  
            this.pingpongTween = Tween.to(this.owner, { "x": -50 }, 2000, Ease.linearNone, Handler.create(this, () => {  
                this.pingpong();
            }));
        }));
	} 

    public moveYUp(): void {
        Tween.to(this.baseSprite, { "y": this._positionY }, 2000, Ease.backIn, Handler.create(this, () => {  
            if (this.curEgg != null) {
                if(this.onFoundEggCallback != null) {
                    this.onFoundEggCallback(this.curEgg.eggID)
                }
                this.curEgg = null;
            }
            this.clawFindedEgg = true;
        }));
	} 

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    onLateUpdate(): void {

        if (this._clawState == ClawState.Idle) {
            
        } else if (this._clawState == ClawState.Dropping || this._clawState == ClawState.Pulling) {
            this.lineSprite.height = this.baseSprite.y - this._positionY + this._lineOrignalHeight;
        }
    }

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}