const { regClass, property } = Laya;
import { EggData } from "../RunTimeData/RunTimeData";
import RigidBody = Laya.RigidBody;
import WeldJoint = Laya.WeldJoint;
import Tween = Laya.Tween;
import Ease = Laya.Ease;
import Handler = Laya.Handler;
import { CartoonFace } from "../Component/CartoonFace";
import { MachinePhysics } from "./MachinePhysics";

@regClass('rkcx43mVQBOkTeNwD9ERHQ')
export class Egg extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;
    

    @property(RigidBody)
    public rigidBody : Laya.RigidBody;

    @property(CartoonFace)
    public cartoonFace: CartoonFace;

    public eggID: string;
    public machinePhysics: MachinePhysics;

    public removeJoint() {
        let weldJoint = this.owner.getComponent(WeldJoint)
        if(weldJoint != null) {
            weldJoint.destroy();
        }
    }

    public removeRigidBody() {
        let rigidBody = this.owner.getComponent(RigidBody)
        if(rigidBody != null) {
            rigidBody.destroy();
        }
    }

    public init() {
        if(this.cartoonFace != null && this.eggID != null) {
            const index = this.eggID.charCodeAt(this.eggID.length - 1);
            this.cartoonFace.build(index);
        } else {
            console.log("cartoonFace or eggID is null");
        }
    }

    public buildJoint(otherBody: RigidBody) {
        // let distanceJoint1: DistanceJoint = new DistanceJoint();
        // distanceJoint1.otherBody = this.pickupTestRigidBody;
        // distanceJoint1.selfAnchor = [0.5, 0.5];
        // distanceJoint1.otherAnchor = [this.pickupTestBox.anchorX, this.pickupTestBox.anchorY];
        // distanceJoint1.frequency = 10;
        // distanceJoint1.damping = 0.5;
        // this.curEgg.owner.addComponentInstance(distanceJoint1);

        let weldJoint: WeldJoint = new WeldJoint();
        weldJoint.selfBody = this.rigidBody;
        weldJoint.otherBody = otherBody;
        weldJoint.collideConnected = false;
        weldJoint.anchor = [0.5, 0.5];
        weldJoint.frequency = 1;
        weldJoint.damping = 1;
        this.owner.addComponentInstance(weldJoint);
        this.rigidBody.allowRotation = true;
    }

    public clear() {
        this.removeRigidBody();
        Tween.to(this.owner, {"y": this.owner.y + 1000}, 1200 + (Math.random() * 200), Ease.backIn, Handler.create(this, () => {  
            this.owner.destroy();
        }));
    }
    //@property(String)
    //public text: string = "";

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    //onAwake(): void {}

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    onUpdate(): void {
        if(this.owner.y > 3000) {
            this.machinePhysics.removeEgg(this.eggID);
            console.log("force remove egg " + this.eggID)
        }
    }

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}