import { UIManager } from "./UIManager";
import Tween = Laya.Tween;
import Ease = Laya.Ease;

export abstract class UIBase extends Laya.Script {

    declare owner : Laya.Sprite;
    private _view: fgui.GComponent;
    private _config: any; 
    private _originScaleX: number;
    private _originScaleY: number;
    private _param: any;

    protected _uiManager: UIManager;

    public set uiManager(uiManager: UIManager) {
        this._uiManager = uiManager;
    }

    public get uiManager() : UIManager {
        return this._uiManager;
    }

    constructor() {  
        super();
        this._view = null;
    }

    public get view(): fgui.GComponent {
        return this._view;
    }

    public set view(value: fgui.GComponent) {
        this._view = value;
    }

    public set param (data: any) {
        this._param = data;
    }

    public get param () : any {
        return this._param;
    }

    public get config(): any {
        return this._config;
    }

    public set config(value: any) {
        this._config = value;
    }

    public initUI() : void{
        let rootAnimation = this.view;//.getChild("Root_Animation")
        if (rootAnimation) {
            //console.log("Root_Animation: " + rootAnimation.name);
            rootAnimation.displayObject.anchorX = 0.5;
            rootAnimation.displayObject.anchorY = 0.5;
            rootAnimation.displayObject.x = this.uiManager.pageWidth * 0.5;
            rootAnimation.displayObject.y = this.uiManager.pageHeight * 0.5;
            this._originScaleX = rootAnimation.displayObject.scaleX;
            this._originScaleY = rootAnimation.displayObject.scaleY;
        }

        let bBackgourndCloseButton = this.view.getChild("B_BackgroundClose");
        if(bBackgourndCloseButton) {
            bBackgourndCloseButton.onClick(this, this.pop);
        }

        let bCloseButton = this.view.getChild("B_Close");
        if(bCloseButton) {
            bCloseButton.onClick(this, this.pop);
        }

        let bOkButton = this.view.getChild("B_OK");
        if(bOkButton) {
            bOkButton.onClick(this, this.pop);
        }
    }

    public open(data: any) : void {
        this.param = data;
        this.willOpen();
        this.enabled = true;
        this.view.visible = true;
        this.didOpen();
    }

    public isOpened() : boolean {
        return this.enabled;
    }

    public willOpen() : void {

        if(this._config.withBackground) {
            this.uiManager.openBackground();

            this.view.displayObject.scaleX = this._originScaleX;
            this.view.displayObject.scaleY = this._originScaleY;
            Tween.clearAll(this.view.displayObject);
            Tween.from(this.view.displayObject, {"scaleX": 0, "scaleY": 0}, 388, Ease.backOut, Laya.Handler.create(this, () => {
                    
                console.log("Open Tween");

            }));
        }

        //console.log("willOpen ", rootAnimation)
    }

    public didOpen() : void {
        //console.log("didOpen ", rootAnimation)
    }

    public forceClose() : void{
        if(this._config.withBackground) {
            this.uiManager.closeBackground();
        }
        this.willClose();
        this.didClose();
        this.enabled = false;
        this.view.visible = false;
    }

    public pop() {
        this.uiManager.popLastUI();
    }

    public popTips() {
        this.uiManager.popLastTips();
    }

    public close() : void{
        this.willClose();

        if(!this._config.withBackground){
            this.didClose();
            this.enabled = false;
            this.view.visible = false;
            
        } else {
            Tween.clearAll(this.view.displayObject);
            Tween.to(this.view.displayObject, {"scaleX": 0, "scaleY": 0}, 388, Ease.backIn, Laya.Handler.create(this, () => {
                this.uiManager.closeBackground();
                this.didClose();
                this.enabled = false;
                this.view.visible = false;
            }));
        }
    }

    public didClose() : void {

    }

    public willClose() : void{

    }

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    //onAwake(): void {}

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {
        //console.log(this.constructor.name);
    //}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}