import { ConfigManager } from "./ConfigManager";

export class MachineConfig {
    ID: string;
    IsNeedUnLock: boolean;
    UnlockIncubatorCost: string;
    Test: string;
    Prefab: string;
    CollectionList: string;

    public static getConfig(id: string): MachineConfig {
        let config = ConfigManager.instance.getConfig('MachineConfig');
        return config[id];
    }

    public static load() {
        ConfigManager.instance.loadExcel('resources/json/excel/Excel_MachineConfig.json', 'MachineConfig', MachineConfig);
    }
}

