import { UIBackground } from "./HUD/UIBackground";
import { UIClaimItem } from "./HUD/UIClaimItem";
import { UICollection } from "./HUD/UICollection";
import { UIFXCoinBackground } from "./HUD/UIFXCoinBackground";
import { UIFXLayer } from "./HUD/UIFXLayer";
import { UIInviteFriends } from "./HUD/UIInviteFriends";
import { UILoading } from "./HUD/UILoading";
import { UIMessageBoxOne } from "./HUD/UIMessageBoxOne";
import { UIShowEggIncuabtion } from "./HUD/UIShowEggIncuabtion";
import { UIShowItem } from "./HUD/UIShowItem";
import { UITask } from "./HUD/UITask";
import { UITips } from "./HUD/UITips";
import { UIUnlockEggIncuabtion } from "./HUD/UIUnlockEggIncuabtion";
import { UIBase } from "./UIBase";

type UIClassMap = {  
    [key: string]: new () => UIBase;
};

export enum UIClassName {  
    UI_FX_Layer_HUD = "UI_FX_Layer_HUD",  
    UI_Loading_HUD = "UI_Loading_HUD",  
    UI_MessageBoxOne_HUD = "UI_MessageBoxOne_HUD",  
    UI_Invite_Friends_HUD = "UI_Invite_Friends_HUD",  
    UI_FX_Coin_Background_HUD = "UI_FX_Coin_Background_HUD",  
    UI_Tips_HUD = "UI_Tips_HUD",  
    UI_Task_HUD = "UI_Task_HUD",  
    UI_Background_HUD = "UI_Background_HUD",  
    UI_ShowItem_HUD = "UI_ShowItem_HUD",
    UI_ShowEggIncuabtion_HUD = "UI_ShowEggIncuabtion_HUD",
    UI_UnlockEggIncuabtion_HUD = "UI_UnlockEggIncuabtion_HUD",
    UI_Collection_HUD = "UI_Collection_HUD",
    UI_ClaimItem_HUD = "UI_ClaimItem_HUD"
} 

export class UIDefine {
    
    private _classMap: UIClassMap = {  
        "UI_FX_Layer_HUD": UIFXLayer,
        "UI_Loading_HUD": UILoading,
        "UI_MessageBoxOne_HUD": UIMessageBoxOne,
        "UI_Invite_Friends_HUD": UIInviteFriends,
        "UI_FX_Coin_Background_HUD": UIFXCoinBackground,
        "UI_Tips_HUD": UITips,
        "UI_Task_HUD": UITask,
        "UI_Background_HUD":UIBackground,
        "UI_ShowItem_HUD":UIShowItem,
        "UI_ShowEggIncuabtion_HUD":UIShowEggIncuabtion,
        "UI_UnlockEggIncuabtion_HUD":UIUnlockEggIncuabtion,
        "UI_Collection_HUD":UICollection,
        "UI_ClaimItem_HUD":UIClaimItem
    }; 

    public instantiate(className: string): UIBase | undefined {  
        const clazz = this._classMap[className];  
        return clazz ? new clazz() : undefined;  
    }
}

