import { ConfigManager } from "./ConfigManager";

export class EggConfig {
    ID: string;
    IncubationTime: number;
    Prefab: string;
    Texture: string;
    Scale: number;
    Sfx: string;
    ClientHandle: string;

    public static getConfig(id: string): EggConfig {
        let config = ConfigManager.instance.getConfig('EggConfig');
        return config[id];
    }

    public static load() {
        ConfigManager.instance.loadExcel('resources/json/excel/Excel_EggConfig.json', 'EggConfig', EggConfig);
    }
}

