import { ConfigManager } from "./ConfigManager";

export class ItemConfig {
    ID: string;
    Rank: number;
    Count: number;
    Name: string;
    Discription: string;
    ClientHandle: string;
    Texture: string;
    Color: string;
    Sfx: string;
    SellingParam: string;

    public static getConfig(id: string): ItemConfig {
        let config = ConfigManager.instance.getConfig('ItemConfig');
        return config[id];
    }

    public static load() {
        ConfigManager.instance.loadExcel('resources/json/excel/Excel_ItemConfig.json', 'ItemConfig', ItemConfig);
    }
}

