
const { regClass, property } = Laya;
import { Machine } from "../GameLogic/Machine";
import { RunTimeData } from "./RunTimeData";

@regClass('fcV-m1clTpK88bUEiz8Jyg')
export class RunTimeDataManager extends Laya.Script {
    declare owner: Laya.Sprite;
    private static _instance: RunTimeDataManager;  

    // 保存数据的对象
    private _data: RunTimeData;

    // 正在玩的娃娃机
    private _machine: Machine

    // 回调函数存储
    private _callbackMap: { [module: string]: (() => void)[] };

    constructor() {  
        super();
        this._data = this.initializeData();
        this._callbackMap = {}; // 初始化回调存储
    }

    public get runTimeData(): RunTimeData {
        return this._data;
    }

    public set machine(value: Machine) {
        this._machine = value;
    }

    public get machine(): Machine {
        return this._machine;
    }

    // 初始化数据
    private initializeData(): RunTimeData {
        return new RunTimeData();
    }

    public static get instance(): RunTimeDataManager {  
        if (!RunTimeDataManager._instance) {  
            return null;
        }  
        return RunTimeDataManager._instance;  
    }  

    onAwake(): void {
        if (RunTimeDataManager._instance) {
            throw new Error("Singleton class. Use getInstance() instead.");
        }
        RunTimeDataManager._instance = this;
    }

    // 深度合并数据
    private mergeDeep(
        target: any,
        source: any
    ): any {
        for (const key in source) {
            if (source[key] && typeof source[key] === 'object' && !Array.isArray(source[key])) {
                if (!target[key]) {
                    target[key] = {};
                }
                // 如果有模块级回调，保存初始状态
                const originalTarget = JSON.stringify(target[key]);
                
                this.mergeDeep(target[key], source[key]);

                // 检查整个模块是否变化
                if (originalTarget !== JSON.stringify(target[key])) {
                    this.invokeModuleCallbacks(key); // 调用模块级回调
                }
            } else {
                const isChanged = target[key] !== source[key];
                target[key] = source[key];
                // 字段级回调
                if (isChanged) {
                    this.invokeModuleCallbacks(key);
                }
            }
        }
        return target;
    }

    // 保存数据，支持部分更新
    public saveData(newData: Partial<RunTimeData>): void {
        this.mergeDeep(this._data, newData); // 更新当前数据
    }

    // 注册回调
    public registerModuleCallback(module: string, callback: () => void): void {
        if (!this._callbackMap[module]) {
            this._callbackMap[module] = [];
        }
        this._callbackMap[module].push(callback);
    }

    // 反注册回调
    public unregisterModuleCallback(module: string, callback: () => void): void {
        if (this._callbackMap[module]) {
            this._callbackMap[module] = this._callbackMap[module].filter(cb => cb !== callback);
        }
    }

    private invokeModuleCallbacks(module: string): void {
        if (this._callbackMap[module]) {
            this._callbackMap[module].forEach(callback => callback());
        }
    }
    // public getData<T>(clazz: new () => T): { [key: string]: T } {
    //     return this._configs[clazz.name];
    // }

    // public getData(name: string) {
    //     return this._datas[name];
    // }
}
