import { HttpManager } from "../Http/HttpManager";
import { UIManager } from "../UI/UIManager";
import { UIClassName } from "../UI/UIDefine";
import { GameStateBase } from "./GameStateBase";
import { RunTimeData } from "../RunTimeData/RunTimeData";
import { RunTimeDataManager } from "../RunTimeData/RunTimeDataManager";
import { ConfigManager } from "../Config/ConfigManager";
import { MachineConfig } from "../Config/ExcelMachine";
import { SoundFXManager } from "../Component/SoundManager";

export class MainMenuState extends GameStateBase {
    enter(): void {
        console.log("Entering MainMenuState");

        const machineID = RunTimeDataManager.instance.runTimeData.machine_data.id;
        const machineConfig = MachineConfig.getConfig(machineID)

        Laya.loader.load(machineConfig.Prefab).then(res => {
            let sprite : Laya.Sprite = res.create();
            this.gameStateManager.ownerPanel.addChild(sprite);
        });  

        UIManager.instance.ShowTips(`Welcome come! ${RunTimeDataManager.instance.runTimeData.user.nick_name}`);

        SoundFXManager.playMusic("MUSIC_1000");
        SoundFXManager.setVolume(0.2);
    }

    exit(): void {
        console.log("Exiting MainMenuState");

        //UIManager.instance.closeUI("UI_Main_Menu_HUD");
        //UIManager.instance.closeUI("UI_FX_Coin_Background_HUD");
    }

    update(): void {
        
    }
}