import { SoundFXManager } from "../../Component/SoundManager";
import { ItemConfig } from "../../Config/ExcelItem";
import { FXExplosion } from "../../Particle/FXExplosion";
import { UIBase } from "../UIBase";

export class UIClaimItem extends UIBase {
    
    private _loaderItemIcon : fgui.GLoader;
    private _imgWindow : fgui.GImage;
    private _textCount : fgui.GLabel;
    private _textName : fgui.GLabel;

    private _originX: number;
    private _originY: number;
    private _fxRoot : fgui.GGraph;
    private _fxExplosion: FXExplosion;

    public initUI(): void {
        super.initUI();

        this._loaderItemIcon = this.view.getChild("LOADER_ItemIcon").asLoader;
        this._imgWindow = this.view.getChild("IMG_Window").asImage;
        this._textCount = this.view.getChild("TEXT_Count").asLabel;
        this._textName = this.view.getChild("TEXT_Name").asLabel;
        this._fxRoot = this.view.getChild("IMG_FXRoot").asGraph;

        this._originX = this._loaderItemIcon.displayObject.x;
        this._originY = this._loaderItemIcon.displayObject.y;
        
        Laya.loader.load("resources/prefabs/fx/P_FXExplosion.lh").then(res => {
            let sprite: Laya.Sprite = res.create();
            sprite.anchorX = 0.5;
            sprite.anchorY = 1;

            this._fxRoot.displayObject.addChild(sprite);

            // 获取 icon 和 sprite 的宽高
            let aWidth = this._fxRoot.displayObject.width;
            let aHeight = this._fxRoot.displayObject.height;

            let bWidth = sprite.width;
            let bHeight = sprite.height;

            // 计算宽高比例
            let scaleX = aWidth / bWidth;
            let scaleY = aHeight / bHeight;

            // 取最小的比例来保证等比缩放
            let scale = Math.min(scaleX, scaleY);

            // 应用等比缩放
            sprite.scale(scale, scale, true);

            // 将 sprite 放置在 icon 的中心位置
            sprite.pos(aWidth, aHeight / 2);

            this._fxExplosion = sprite.getChildAt(0).getComponent(FXExplosion);

        });
    }

    public didClose(): void {
        super.didClose();

        this.clearIcon();
        this.stopIconBounceAnimation();
    }

    public didOpen(): void {
        this.buildIcon(this.param.itemID, this.param.count);
        this._fxExplosion.emit();
    }

    private buildIcon(itemID: string, count: number) {

        const itemConfig = ItemConfig.getConfig(itemID)
        if(itemConfig) {
            this._loaderItemIcon.url = itemConfig.Texture;
            this._loaderItemIcon.displayObject.anchorX = 0.5;
            this._loaderItemIcon.displayObject.anchorY = 0.5;
            this._loaderItemIcon.displayObject.x = this._originX + this._loaderItemIcon.displayObject.width * 0.5;
            this._loaderItemIcon.displayObject.y = this._originY + this._loaderItemIcon.displayObject.height * 0.5;

            this._textName.text = itemConfig.Name;
            this._textCount.text = `x ${count}`;
            this._imgWindow.color = itemConfig.Color;

            this.startIconBounceAnimation();

            SoundFXManager.playSoundFX("SFX_ClaimItem");
            SoundFXManager.playSoundFX(itemConfig.Sfx);
        }
    }

    public clearIcon() {
        this._loaderItemIcon.url = "";
    }

    // 停止跳动动画
    private stopIconBounceAnimation() {
        if (this._loaderItemIcon) {
            Laya.Tween.clearTween(this._loaderItemIcon.displayObject); // 清除 this.icon 上的所有动画
        }
    }

    // 给icon增加一个跳动的动画效果
    private startIconBounceAnimation() {
        // 首先停止之前可能存在的动画
        this.stopIconBounceAnimation(); 

        // 动画函数：让icon缩放变大并稍微上移，带有弹力效果
        const bounce1 = () => {
            Laya.Tween.to(this._loaderItemIcon.displayObject, { scaleX: 1.1, scaleY: 0.8 }, 1000, Laya.Ease.linearInOut, Laya.Handler.create(this, bounce2));
        };

        // 动画函数：让icon缩放变小并回到原位置，带有弹力效果
        const bounce2 = () => {
            Laya.Tween.to(this._loaderItemIcon.displayObject, { scaleX: 1, scaleY: 1 }, 1000, Laya.Ease.linearInOut, Laya.Handler.create(this, bounce1));
        };

        // 开始第一次动画
        bounce1();
    }
}