export class CountdownTimer {
    private _endTime: number;  // Server's UTC timestamp in milliseconds
    private onStartedCallback: (remainingTime: string, difference: number) => void;
    private onTickCallback: (remainingTime: string, difference: number) => void;
    private onCompleteCallback: () => void;
    

    constructor(
        serverEndTime: number, // Server-provided UTC timestamp
        onStartedCallback: (remainingTime: string, difference: number) => void,
        onTickCallback: (remainingTime: string, difference: number) => void,
        onCompleteCallback: () => void,
    ) {
        this._endTime = serverEndTime; // Adjusted end time
        this.onStartedCallback = onStartedCallback;
        this.onTickCallback = onTickCallback;
        this.onCompleteCallback = onCompleteCallback;
    }

    public restart(serverEndTime: number) {
        this._endTime = serverEndTime; // Adjusted end time
        this.start();
    }

    // Start the countdown
    public start() {
        // Use Laya.timer to loop the countdown
        Laya.timer.loop(1000, this, this.update);
        const remainingTime = this.getRemainingTime();
        this.onStartedCallback(this.formatTime(remainingTime), remainingTime);
    }

    private getRemainingTime() {
        const currentTime = Math.floor(Date.now() / 1000); // Convert to seconds
        return this._endTime - currentTime;
    }

    // Update the countdown logic every second
    private update() {
        const remainingTime = this.getRemainingTime();
        this.onTickCallback(this.formatTime(remainingTime), remainingTime);
        
        if (remainingTime <= 0) {
            this.stop();
            this.onCompleteCallback();
        }
    }

    // Stop the countdown
    public stop() {
        Laya.timer.clear(this, this.update); // Clear the timer loop
    }

    // Format the remaining time in hh:mm:ss format
    private formatTime(remainingTime: number): string {
        const totalSeconds = remainingTime;
        const hours = Math.floor(totalSeconds / 3600);
        const minutes = Math.floor((totalSeconds % 3600) / 60);
        const seconds = totalSeconds % 60;

        return `${hours.toString().padStart(2, '0')}:${minutes
            .toString()
            .padStart(2, '0')}:${seconds.toString().padStart(2, '0')}`;
    }
}
