import { HttpManager } from "../Http/HttpManager";
import { GameStateBase } from "./GameStateBase";
import { LoadingState } from "./LoadingState";
import { MainMenuState } from "./MainMenuState";

const { regClass, property } = Laya;

export enum GameState {
    LOADING,
    MAINMENU,
}

@regClass('IjMsviyZT9-45baRHQ_jAg')
export class GameStateManager extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;
    private static _instance: GameStateManager;  
    private _states: { [key in GameState]: GameStateBase } = {} as any;
    private _currentState: GameStateBase;

    public ownerPanel: Laya.Box;
    //构造函数在编辑器期间也会执行
    constructor() {  
        super();
    }

    public static get instance(): GameStateManager {  
        if (!GameStateManager._instance) {  
            return null;
        }  
        return GameStateManager._instance;  
    }  

    public changeState(newState: GameState): void {
        if (this._currentState) {
            this._currentState.exit();
        }
        this._currentState = this._states[newState];
        this._currentState.enter();
    }

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    onAwake(): void {

        if (GameStateManager._instance) {
            throw new Error("Singleton class. Use getInstance() instead.");
        }
        GameStateManager._instance = this;
        this.ownerPanel = this.owner as Laya.Panel;

        this._states[GameState.LOADING] = new LoadingState(this);
        this._states[GameState.MAINMENU] = new MainMenuState(this);

        this.changeState(GameState.LOADING);
    }

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    public onUpdate(): void {
        if (this._currentState) {
            this._currentState.update();
        }
    }

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}