const { regClass, property } = Laya;

@regClass('_XACmj2HQ7OSH2UUagT4-Q')
export class Splash {
    
    public static instance: Laya.Sprite;
    
    public static openSplash(): void {
        Laya.loader.load("resources/prefabs/ui/P_Splash.lh").then(res => {
            
            let sprite : Laya.Sprite = res.create();
            sprite.anchorX = 0.5;
            sprite.anchorY = 0.5;
            Laya.stage.addChild(sprite);

            const pageWidth = Laya.stage.designHeight * Laya.Browser.clientWidth / Laya.Browser.clientHeight;
            const pageHeight = Laya.stage.designHeight;

            let aWidth = pageWidth;
            let aHeight = pageHeight;

            let bWidth = sprite.width;
            let bHeight = sprite.height;

            // 计算宽高比例
            let scaleX = aWidth / bWidth;
            let scaleY = aHeight / bHeight;

            // 取最小的比例来保证等比缩放
            let scale = Math.min(scaleX, scaleY);

            // 应用等比缩放
            //sprite.scale(scale, scale, true);
            
            // 将 sprite 放置在 icon 的中心位置
            sprite.pos(aWidth / 2, aHeight / 2);
            //sprite.anchorY = 1;

            Splash.instance = sprite;
        }); 
    }

    public static closeSplash(): void {
        if(Splash.instance) {
            Splash.instance.destroy();
            Splash.instance = null;
        }
    }
    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    //onAwake(): void {}

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //组件被禁用时执行，例如从节点从舞台移除后
    //onDisable(): void {}

    //第一次执行update之前执行，只会执行一次
    //onStart(): void {}

    //手动调用节点销毁时执行
    //onDestroy(): void {}

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onLateUpdate(): void {}

    //鼠标点击后执行。与交互相关的还有onMouseDown等十多个函数，具体请参阅文档。
    //onMouseClick(): void {}
}