const { regClass, property } = Laya;

import { Claw } from "./Claw";
import Sprite = Laya.Sprite;
import Event = Laya.Event;
import Tween = Laya.Tween;
import Ease = Laya.Ease;
import Handler = Laya.Handler;
import Point = Laya.Point;
import Label = Laya.Label;
import Keyboard = Laya.Keyboard;
import { MachinePhysics } from "./MachinePhysics";
import { RunTimeDataManager } from "../RunTimeData/RunTimeDataManager";
import { TransitionTool } from "../Utils/TransitionTool ";
import { CountdownTimer } from "../Component/CountdownTimer";
import { HttpManager } from "../Http/HttpManager";
import { HttpURL } from "../Http/HttpDefine";
import { UIManager } from "../UI/UIManager";
import { EggConfig } from "../Config/ExcelEgg";
import { IncubationSlot } from "./IncubationSlot";
import { CoinFlyEffect } from "../FX/CoinFlyEffect";
import { UIClassName } from "../UI/UIDefine";
import { ItemConfig } from "../Config/ExcelItem";
import { Arrow } from "./Arrow";
import { SoundFXManager } from "../Component/SoundManager";

@regClass('m2kViDcTTYmr8Ky4zATsSw')
export class Machine extends Laya.Script {
    //declare owner : Laya.Sprite3D;
    declare owner : Laya.Sprite;

    @property(Claw)
    public claw: Claw;

    @property(Sprite)
    public bGrab: Sprite;

    @property(Sprite)
    public bCollection: Sprite;

    @property(Sprite)
    public background: Sprite;

    @property(Sprite)
    public grassGround: Sprite;

    @property({ type : Number })
    public backgroundOffsetScale : number = 0.1;

    @property(Label)
    public textPoint: Label;

    @property(Label)
    public textGem: Label;

    @property(Sprite)
    public pointIcon: Sprite;
    private _pointFlyEffect: CoinFlyEffect;

    @property(Sprite)
    public gemIcon: Sprite;
    private _gemFlyEffect: CoinFlyEffect;

    @property(Label)
    public textResetCooldownTime: Label;
    private _resetCoolDownTimer : CountdownTimer;

    @property(Label)
    public textGameCoinCooldownTime: Label;
    private _gameCoinCoolDownTimer : CountdownTimer;

    @property(MachinePhysics)
    public machinePhysics : MachinePhysics;

    @property([IncubationSlot])
    public incubationSlotList: IncubationSlot[];

    @property(Arrow)
    public arrow: Arrow;

    private _arrowDelayTimeCofig = 15;
    private _arrowDelayTime = this._arrowDelayTimeCofig;
    
    private _backgroundTime : number = 0;
    private _machineID: string;
    
    onDisable(): void {
        
    }

    //组件被激活后执行，此时所有节点和组件均已创建完毕，此方法只执行一次
    onAwake(): void {
        this._backgroundTime = 0;
        this.claw.onFoundEggCallback = this.foundEgg;

        this._pointFlyEffect = new CoinFlyEffect(20, this.pointIcon, 160, "SFX_Gold03")
        this._gemFlyEffect = new CoinFlyEffect(20, this.gemIcon, 180, "SFX_Gem01")
    }

    //组件被启用后执行，例如节点被添加到舞台后
    //onEnable(): void {}

    //第一次执行update之前执行，只会执行一次
    onStart(): void {
        
        this.bGrab.on(Event.CLICK, this, this.onClickGrab);
        this.bCollection.on(Event.CLICK, this, this.onClickCollection);

        this._machineID = RunTimeDataManager.instance.runTimeData.machine_data.id;
        
        this.initTimeShower();

        this.eventListener();

        this.updateDisplay();

        this.initOrRefreshGameCoinTimeShower();

        //设定槽位的信息
        for(let i = 0; i < this.incubationSlotList.length; i++) {
            this.incubationSlotList[i].onHarvestEggCallback = this.harvestEgg;
        }

        //保存实例引用
        RunTimeDataManager.instance.machine = this;
    }

    initTimeShower() {
        const machineData = RunTimeDataManager.instance.runTimeData.machine_data;

        if(this._resetCoolDownTimer != null) {
            this._resetCoolDownTimer.restart(machineData.next_refresh_at.T)
            return;
        }

        this._resetCoolDownTimer = new CountdownTimer(machineData.next_refresh_at.T, 
            (remainingTime, difference) => {
                this.textResetCooldownTime.text = `${remainingTime}`
            },
            (remainingTime, difference) => {

                this.textResetCooldownTime.text = `${remainingTime}`
            },
            () => {
                //console.log("Countdown completed!");

                //清空所有蛋,重新初始化新的蛋
                this.machinePhysics.clearAllEggs();

                const machineID = machineData.id;
                RunTimeDataManager.instance.runTimeData.resetMachineData();
                
                Laya.timer.once(2000, this, this.resetEggs);
            }
        );
        this._resetCoolDownTimer.start();
    }


    initOrRefreshGameCoinTimeShower() {
        const machineCoinData = RunTimeDataManager.instance.runTimeData.machine_coin_data;

        machineCoinData.updateGameCoin();

        if(this._gameCoinCoolDownTimer != null) {
            this._gameCoinCoolDownTimer.restart(machineCoinData.game_coin_next_update_at.T)
            return;
        }

        this._gameCoinCoolDownTimer = new CountdownTimer(machineCoinData.game_coin_next_update_at.T, 
            (remainingTime, difference) => {
                this.textGameCoinCooldownTime.text = `${remainingTime}`
            },
            (remainingTime, difference) => {

                this.textGameCoinCooldownTime.text = `${remainingTime}`
            },
            () => {
                //console.log("Countdown completed!");
                this.initOrRefreshGameCoinTimeShower();
                this.updateDisplay();
            }
        );
        this._gameCoinCoolDownTimer.start();
    }

    resetEggs() {

        //默认写死的,后面更新
        const params = { 
            machine_id: this._machineID,
        }; 

        //获取娃娃机的信息
        HttpManager.instance.post(HttpURL.update_machine_data, params, (response: any) => {  
            this.machinePhysics.createEggs();
            this.initTimeShower();
        }, (error: any) => {  
            UIManager.instance.ShowMessageBox(error.err_msg, null);
        });        
    }

    updateDisplay() {
        //刷新顶部的条
        const wallet = RunTimeDataManager.instance.runTimeData.wallet;
        TransitionTool.transitionValue(this.textPoint, wallet.point, 2000);
        TransitionTool.transitionValue(this.textGem, wallet.gem, 2000);

        this.claw.updateGameCoinDisplay();

        //刷新孵化器的条目
        const machineData = RunTimeDataManager.instance.runTimeData.machine_data;
        for(let i = 0; i < machineData.incubation_list.length; i++) {
            this.incubationSlotList[i].index = i;
            this.incubationSlotList[i].updateSlot(machineData.incubation_list[i]);
        }
    }

    onClickGrab(e: Event = null): void {
        SoundFXManager.playSoundFX("SFX_ButtonClick01");
        this.claw.grab();
        this.arrow.owner.visible = false;
        this._arrowDelayTime = this._arrowDelayTimeCofig;
    }

    onClickCollection(e: Event = null): void {
        SoundFXManager.playSoundFX("SFX_ButtonClick01");
        UIManager.instance.pushUI(UIClassName.UI_Collection_HUD, null);
    }

    //每帧更新时执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    //onUpdate(): void {}

    //每帧更新时执行，在update之后执行，尽量不要在这里写大循环逻辑或者使用getComponent方法
    onLateUpdate(): void {
        let deltaTime = Laya.timer.delta / 1000;
        this._backgroundTime += deltaTime * this.backgroundOffsetScale;
        this.background.material.setFloat("u_Time", this._backgroundTime)
        this.grassGround.material.setFloat("u_Time", -this._backgroundTime)
        if(this._backgroundTime > Number.MAX_SAFE_INTEGER) {
            this._backgroundTime = 0;
        }

        if(this.arrow.owner.visible == false) {
            this._arrowDelayTime -= deltaTime;
            if(this._arrowDelayTime <= 0) {
                this.arrow.owner.visible = true;
            }
        }
    }

    public foundEgg = (eggID: string) => {

        //先判断池子有没有东西,如果有东西就不发送网络请求了
        const machineData = RunTimeDataManager.instance.runTimeData.machine_data;
        if (machineData.egg_list && machineData.egg_list[eggID]) {
            //return;
        } else {
            return;
        }

        const eggData = machineData.egg_list[eggID];
        const eggConfig = EggConfig.getConfig(eggData.config_id);
        const egg = this.machinePhysics.getEgg(eggID);

        let eggWorldPos: Point = Point.create();
        eggWorldPos.x = egg.owner.width * egg.owner.anchorX;
        eggWorldPos.y = egg.owner.height * egg.owner.anchorY;
        eggWorldPos = egg.owner.localToGlobal(eggWorldPos);

        const totalGameCoin = RunTimeDataManager.instance.runTimeData.machine_coin_data.getTotalGameCoin();

        if(totalGameCoin <= 0) {
            egg.removeJoint();
            UIManager.instance.ShowMessageBox("No enough game coin", null);
            SoundFXManager.playSoundFX("SFX_Failed");
            return;
        }

        if (machineData.hasEmptyIncubationSlot() == false && eggConfig.IncubationTime > 0) {
            egg.removeJoint();
            UIManager.instance.ShowMessageBox("No empty incubation slot", null);
            SoundFXManager.playSoundFX("SFX_Failed");
            return;
        }

        //发送网络消息
        const params = {
            "machine_id": machineData.id,
            "egg_id":eggID
        }

        HttpManager.instance.post(HttpURL.found_egg, params, (response: any) => {  

            this.updateDisplay();
            this.initOrRefreshGameCoinTimeShower();
            this.handleFoundEgg(response, eggWorldPos.x, eggWorldPos.y);
            //这里要打开一个计时器页面
            //console.log(RunTimeDataManager.instance.runTimeData.wallet.point);
            
        }, (error: any) => {  
            UIManager.instance.ShowMessageBox(error.err_msg, null);
        }); 
        
        this.machinePhysics.removeEgg(eggID);
    }

    public handleFoundEgg(response: any, eggX: number, eggY: number)
    {
        const eggConfig = EggConfig.getConfig(response.response.egg_config_id)
        switch(eggConfig.ClientHandle) {
            case "point": 
                {
                    SoundFXManager.playSoundFX("SFX_ItemDrop");
                    UIManager.instance.pushUI(UIClassName.UI_ClaimItem_HUD, {
                        itemID: response.response.loot_item_id,
                        count: response.response.loot_item_count
                    });
                    this._pointFlyEffect.startEffect(eggX, eggY);
                }
                break;
            case "gem": 
                {
                    SoundFXManager.playSoundFX("SFX_ItemDrop");
                    UIManager.instance.pushUI(UIClassName.UI_ClaimItem_HUD, {
                        itemID: response.response.loot_item_id,
                        count: response.response.loot_item_count
                    });
                    this._gemFlyEffect.startEffect(eggX, eggY);
                }
                break;
                
            case "egg":
                {
                    UIManager.instance.pushUI(UIClassName.UI_ShowEggIncuabtion_HUD, {
                        incubationIndex: response.response.incubation_index
                    });
                }
                break;
        }
    }

    public harvestEgg = (soltIndex: number, worldPos: Point) => {

        const machineData = RunTimeDataManager.instance.runTimeData.machine_data
        //发送网络消息
        const params = {
            "machine_id": machineData.id,
            "incubation_index":soltIndex
        }

        HttpManager.instance.post(HttpURL.harvest_egg, params, (response: any) => {  

            this.updateDisplay();
            //这里要打开一个收获页面
            this.handleHarvestEgg(response, worldPos.x, worldPos.y);

        }, (error: any) => {  
            UIManager.instance.ShowMessageBox(error, null);
        }); 
    }

    public handleHarvestEgg(response: any, eggX: number, eggY: number)
    {
        UIManager.instance.pushUI(UIClassName.UI_ClaimItem_HUD, {
            itemID: response.response.loot_item_id,
            count: response.response.loot_item_count
        });

        const config = ItemConfig.getConfig(response.response.loot_item_id)
        switch(config.ClientHandle) {
            case "point": 
                {
                    this._pointFlyEffect.startEffect(eggX, eggY);
                }
                break;
                
            case "gem": 
                {
                    this._gemFlyEffect.startEffect(eggX, eggY);
                }
                break;
                
            case "collection":
                {
                    
                }
                break;
        }
    }

    eventListener() {
       
        Laya.stage.on(Event.KEY_DOWN, this, (e: any = null) => {
            switch (e.keyCode) {

                case Keyboard.DOWN:
                    //this.claw.stop();
                    this.claw.grab();
                    break;
            }
        });
    }

}