import { WebMessage } from "../Component/WebMessage";
import { EggConfig } from "./ExcelEgg";
import { ItemConfig } from "./ExcelItem";
import { MachineConfig } from "./ExcelMachine";

const { regClass, property } = Laya;

export class ConfigHelper {
    public static parseStringArray(data: string): string[] {
        return data.split(',');
    }

    public static parseNumberArray(data: string): number[] { 
        return data.split(',').map(Number); // 将字符串转换为整数
    }
}

@regClass('YmQd5OnFTbiJU_nWX1ol4w')
export class ConfigManager extends Laya.Script {
    declare owner: Laya.Sprite;
    private static _instance: ConfigManager;  
    private _initialized: boolean = false;

    private _configs: Record<string, any> = {}; // 或者 { [key: string]: any }

    constructor() {  
        super();
    }

    public static get instance(): ConfigManager {  
        if (!ConfigManager._instance) {  
            return null;
        }  
        return ConfigManager._instance;  
    }  

    public get initialized(): boolean {
        return this._initialized;
    }

    onAwake(): void {
        if (ConfigManager._instance) {
            throw new Error("Singleton class. Use getInstance() instead.");
        }
        ConfigManager._instance = this;
    }

    public loadAll() {
        // 示例：调用 LoadExcel 方法加载配置
        EggConfig.load();
        ItemConfig.load();
        MachineConfig.load();

        this._initialized = true;
    }

    public loadExcel<T>(excelPath: string, className: string, clazz: new () => T) {
        Laya.loader.load(excelPath, Laya.Handler.create(this, (data: any) => {
            const itemData: any[] = data.data;
            const itemMap: { [key: string]: T } = {};
            
            itemData.forEach(item => {
                const instance = new clazz();
                Object.assign(instance, item);
                itemMap[item.ID] = instance; 
            });
            
            this._configs[className] = itemMap;

            const message = { type: 'GAME_LOG', data: `${excelPath} success ${clazz.name}` }; 
            WebMessage.sendMessageToWeb(message);

        }), null, Laya.Loader.JSON);
    }

    //public getData<T>(clazz: new () => T): { [key: string]: T } {
    //    return this._configs[clazz.name];
    //}

    public getConfig(name: string) {
        return this._configs[name];
    }
}
