import { SystemDefine } from "../SystemDefine";

export class ServerTime {
    T: number;
    I: number;
}

export class ItemData {
    item_list: any | null;
    machine_collection_data: {
        [key: string]: {
            item_list: any | null;
        };
    };
}

export class UserData {
    created_at: {
        T: number;
        I: number;
    };
    updated_at: {
        T: number;
        I: number;
    };
    nick_name: string;
    faction: number;
    unlock_land_level: number;
    user_type: number;
    first_name: string;
    last_name: string;
    help_count: number;
}

export class WalletData {
    point: number;
    gem: number;
}

export class EggData {
    config_id: string;
}

export class IncubationEntry {
    egg_id:string;
    config_id: string;
    is_in_progress: boolean;
    maturation_at: ServerTime;
}

export class MachineData {
    id: string;
    egg_list: {
        [eggId: string]: EggData;
    };
    next_refresh_at: ServerTime;
    incubation_list: IncubationEntry[];
    egg_loot_list: Record<string, unknown>; // 可以根据需要进一步定义具体结构

    public hasEmptyIncubationSlot(): boolean {
        return this.incubation_list.some(entry => !entry.is_in_progress);
    }

    public getIncubationDataCount(): number {
        return this.incubation_list.length;
    }
}

export class MachineCoinData {
    game_coin_last_update_at:ServerTime;
    game_coin_remaining_duration: number;
    game_coin: number;
    payed_game_coin: number;

    //本地变量
    game_coin_next_update_at:ServerTime;

    public getTotalGameCoin(): number {
        return this.game_coin + this.payed_game_coin;
    }

    public updateGameCoin() {
        const currentTime = Math.floor(Date.now() / 1000);
        const elapsedTime = currentTime - this.game_coin_last_update_at.T;

        // 将剩余的未计算时间加到已计算的时间上
	    const totalDuration = elapsedTime + (this.game_coin_remaining_duration);

        const timeParam = SystemDefine.gameCoinInterval;
        const intervals = totalDuration / timeParam;
        this.game_coin += Math.floor(intervals) 
        if (this.game_coin > SystemDefine.gameCoinMax) {
            this.game_coin = SystemDefine.gameCoinMax
        }
        this.game_coin_remaining_duration = totalDuration % timeParam;
        this.game_coin_last_update_at.T = currentTime;

        if(this.game_coin_next_update_at == null){
            this.game_coin_next_update_at = new ServerTime();
        }
        this.game_coin_next_update_at.T = currentTime + timeParam - this.game_coin_remaining_duration;
    }
}

export class MachineCollectionData {
    item_list: any | null;

    public getItemCount(): number {
        if(this.item_list == null) {
            return 0;
        }
        return Object.keys(this.item_list).length;
    }

    public getItemCountById(itemID: string): number {
        if(this.item_list == null) {
            return 0;
        }
        if(this.item_list[itemID] == null) {
            return 0;
        }
        return this.item_list[itemID];
    }
}

export class RunTimeData {
    item: ItemData;
    user: UserData;
    wallet: WalletData;
    machine_data: MachineData;
    machine_coin_data: MachineCoinData;
    machine_collection: MachineCollectionData;

    // 构造函数直接初始化属性
    constructor() {
        this.item = new ItemData();
        this.user = new UserData();
        this.wallet = new WalletData();
        this.machine_data = new MachineData();
        this.machine_coin_data = new MachineCoinData();
        this.machine_collection = new MachineCollectionData();
    }

    resetMachineData() {
        this.machine_data = new MachineData();
    }
}
